import { Injectable } from '@angular/core';
import {ApolloQueryResult} from "@apollo/client";
import {Apollo, MutationResult} from "apollo-angular";
import {Observable} from "rxjs";
import {
  ListItemDto,
  RequestGenerateLinkAgentItemDto,
  ResponseDetailAgentDto, ResponseGenerateLinkAgentItemDto,
  ResponseListItemDto
} from "./ajak-cuan.dto";
import {detailAgent, generateLinkAgentItem, listItem} from "./ajak-cuan.constant";
import {ListApiTypes} from "../../../types/list-api.types";

@Injectable({
  providedIn: 'root'
})
export class ScmAjakCuanService {

  constructor(private apollo: Apollo) {}

  detailAgent(): Observable<ApolloQueryResult<ResponseDetailAgentDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseDetailAgentDto>({
        query: detailAgent,
        fetchPolicy: 'no-cache'
      })
  };

  listItem(data: ListItemDto): Observable<ApolloQueryResult<ResponseListItemDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListItemDto>({
        query: listItem,
        fetchPolicy: 'no-cache',
        variables: data,
      })
  };

  generateLinkAgentItem(data: RequestGenerateLinkAgentItemDto): Observable<MutationResult<ResponseGenerateLinkAgentItemDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseGenerateLinkAgentItemDto>({
        fetchPolicy: 'no-cache',
        mutation: generateLinkAgentItem,
        variables: data
      });
  }

}
