/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @angular-eslint/no-input-rename */
/* eslint-disable @angular-eslint/component-selector */
import {
  ElementRef,
  Input,
  OnInit,
  Component,
  Renderer2,
  Inject,
  PLATFORM_ID
} from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { ValidationService } from './validator.service';
import { isPlatformBrowser } from '@angular/common';

@Component({
  selector: 'control-message',
  template: ` <div
    class="form-control-feedback text-primary-yt-red flex flex-row items-center text-[14px] font-semibold mt-1"
    *ngIf="errorMessage">
    <span>
      <img
        src="/assets/images/png/validator-invalid.png"
        class="w-[14px] mr-[5px]"
        alt="validator-invalid" /></span
    >{{ errorMessage }}
  </div>`
})
export class ControlMessageComponent implements OnInit {
  errorMessage!: string | null;
  @Input('formGroup') private formGroup!: FormGroup;
  @Input('tag') private tag!: string;
  @Input('customErrorMessage') private customErrorMessage!: string;
  private component!: FormControl;
  private inputElement: any = null;
  isReady = false;
  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private el: ElementRef,
    private render: Renderer2
  ) {
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
    }
  }

  ngOnInit(): void {
    let componentName: string;
    let formComponent =
      this.el.nativeElement.parentElement.parentElement.getAttribute(
        'formComponent'
      );

    if (formComponent === null) {
      formComponent =
        this.el.nativeElement.parentElement.getAttribute('formComponent');
    }

    if (formComponent !== null) {
      if (this.checkComponent(formComponent) === 'ddl') {
        // for primeng p-dropdown
        this.inputElement = this.el.nativeElement.parentElement.querySelector(
          'p-dropdown' + ' .ui-dropdown'
        );
      } else if (this.checkComponent(formComponent) === 'autoComplete') {
        // for primeng p-autoComplete
        this.inputElement =
          this.el.nativeElement.parentElement.querySelector('p-autoComplete');
      } else {
        this.inputElement =
          this.el.nativeElement.parentElement.querySelector(formComponent);
      }
    } else {
      this.inputElement = this.el.nativeElement.parentElement.querySelector(
        this.tag
      );
    }

    if (this.inputElement) {
      if (this.checkComponent(formComponent) === 'ddl') {
        // for primeng p-dropdown
        componentName =
          this.inputElement.parentElement.parentElement.getAttribute(
            'formControlName'
          );
      } else if (this.checkComponent(formComponent) === 'autoComplete') {
        // for primeng p-autoComplete
        componentName =
          this.inputElement.parentElement.getAttribute('formControlName');
      } else {
        componentName = this.inputElement.getAttribute('formControlName');
      }
    }

    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-expect-error
    if (!componentName) {
      console.error(
        'ControlMessageComponent: Unable to get the control name. Is the formControlName attribute set correctly?'
      );
      return;
    }

    const control = this.formGroup.get(componentName);
    if (!(control instanceof FormControl)) {
      console.error(
        `ControlMessageComponent: Unable to get the FormControl from the form and the control name: ${componentName}.`
      );
      return;
    }

    this.component = control as FormControl;

    this.component.statusChanges.subscribe((status) => {
      this.onStatusChange(status, this.component.dirty, formComponent);
    });
    this.onStatusChange(
      this.component.status,
      this.component.dirty,
      formComponent
    );
  }

  onStatusChange(status: string, dirty: boolean, formComponent: string): void {
    let labelElement = null;
    let labelVal = 'This field';

    if (this.checkComponent(formComponent) === 'ddl') {
      // for primeng p-dropdown
      labelElement =
        this.el.nativeElement.parentElement.parentElement.parentElement.querySelector(
          'label'
        );
    } else {
      labelElement = this.el.nativeElement.parentElement.querySelector('label');
    }
    if (labelElement && this.isReady) {
      labelVal = labelElement.innerText.replace(/[*]$/, '');
    }

    if (status === 'VALID' && dirty) {
      this.errorInput('valid', formComponent);
      this.errorMessage = null;
    } else if (status === 'INVALID' && dirty) {
      this.errorInput('invalid', formComponent);
      for (const propertyName in this.component.errors) {
        if (
          // eslint-disable-next-line no-prototype-builtins
          this.component.errors.hasOwnProperty(propertyName) &&
          this.component.dirty
        ) {
          this.errorMessage = ValidationService.getValidatorErrorMessage(
            propertyName,
            this.component.errors[propertyName],
            labelVal,
            this.customErrorMessage
          );
        }
      }
    } else {
      this.errorMessage = null;
    }
  }

  errorInput(status: 'invalid' | 'valid', formComponent: string) {
    if (formComponent === 'p-calendar') {
      // for primeng calender
      if (status === 'invalid') {
        this.render.setStyle(
          this.inputElement.querySelector('input'),
          'border',
          'solid 1px red'
        );
      }
      if (status === 'valid') {
        this.render.setStyle(
          this.inputElement.querySelector('input'),
          'border',
          'solid 1px #4dd599'
        );
      }
    } else if (this.checkComponent(formComponent) === 'autoComplete') {
      // for primeng autoComplete
      if (status === 'invalid') {
        this.render.setStyle(
          this.inputElement.querySelector('input'),
          'border',
          'solid 1px red'
        );
      }
      if (status === 'valid') {
        this.render.setStyle(
          this.inputElement.querySelector('input'),
          'border',
          'solid 1px #4dd599'
        );
      }
    } else {
      if (status === 'invalid') {
        // this.render.setStyle(this.inputElement.querySelector('input'), 'border', 'solid 1px red');
        // this.render.setStyle(this.inputElement.querySelector("input"), "box-shadow", "0 0 0 0.2rem #d0021b36");
      }
      if (status === 'valid') {
        // this.render.setStyle(this.inputElement.querySelector('input'), 'border', 'solid 1px #ced4da');
        // this.render.removeStyle(this.inputElement.querySelector("input"), "box-shadow");
        // this.render.removeStyle(inputElement,"border");
      }
    }
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  checkComponent(elementName: string): any {
    if (elementName) {
      const getType = elementName.split('-');
      return getType[0];
    }
  }
}
