import { Injectable } from '@angular/core';
import { Apollo } from 'apollo-angular';
import { ListApiTypes } from '../../../types/list-api.types';
import {
  CurrentTierResponse,
  ListTierActiveResponse,
  TierCardResponse
} from './membership.dto';
import { ApolloQueryResult } from '@apollo/client';
import { Observable } from 'rxjs';
import {
  currentTier,
  listTierActive,
  userTierDetail
} from './membership.constant';

@Injectable({
  providedIn: 'root'
})
export class MembershipService {
  constructor(private apollo: Apollo) {}

  currentTier(): Observable<ApolloQueryResult<CurrentTierResponse>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<CurrentTierResponse>({
        fetchPolicy: 'no-cache',
        query: currentTier
      });
  }

  userTierDetail(): Observable<ApolloQueryResult<TierCardResponse>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<TierCardResponse>({
        query: userTierDetail,
        fetchPolicy: 'no-cache'
      });
  }
  listTierActive(): Observable<ApolloQueryResult<ListTierActiveResponse>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ListTierActiveResponse>({
        query: listTierActive,
        fetchPolicy: 'no-cache'
      });
  }
}
