import { gql } from 'apollo-angular';

export const listStockProduct = gql`
  query listProduct($listProductInput: ListProductInput!) {
    listProduct(listProductInput: $listProductInput) {
      products {
        id
        name
        productType
        productStock {
          id
          quantity
          quantityChanges
          createdAt
          updatedAt
        }
        productStockHistories {
          id
          productStockId
          productId
          quantityIn
          quantityOut
          note
        }
        createdAt
        updatedAt
      }
      limit
      page
      totalPage
      total
    }
  }
`;

export const detailStockProduct = gql`
  query detailProduct($id: String!) {
    detailProduct(id: $id) {
      id
      name
      productType
      productStock {
        id
        quantity
        quantityChanges
        createdAt
        updatedAt
      }
      productStockHistories {
        id
        productStockId
        productId
        quantityIn
        quantityOut
        note
        actionType
      }
      createdAt
      updatedAt
    }
  }
`;

