export const listAddOnItem = gql`
  query listAddOnItem($listAddOnItemInput: ListAddOnItemInput!) {
    listAddOnItem(listAddOnItemInput: $listAddOnItemInput) {
      page
      limit
      total
      totalPage
      addOnItems {
        id
        name
        price
        priceType
        quantity
        isStockActive
        updatedAt
        addOnCategory {
          id
          name
        }
        addOnItemHistories {
          id
          quantityIn
          quantityOut
          actionType
        }
      }
    }
  }
`;

export const detailAddOnItem = gql`
  query detailAddOnItem($id: ID!) {
    detailAddOnItem(id: $id) {
      id
      name
      priceType
      quantity
      isStockActive
      price
      createdAt
      updatedAt
      addOnCategory {
        name
      }
      addOnItemHistories {
        id
        actionType
        quantityIn
        quantityOut
        note
        createdAt
      }
    }
  }
`;
