import { Observable } from "rxjs";
import { ResponseRefreshTokenDto } from "./token.dto";
import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Apollo, MutationResult } from "apollo-angular";
import {refreshTokenMutation} from "./token.constant";
import {ListApiTypes} from "../../../types/list-api.types";

@Injectable({
  providedIn: "root"
})
export class TokenService {
  constructor(
    private httpClient: HttpClient,
    private apollo: Apollo
  ) {
  }


  refreshToken(): Observable<MutationResult<ResponseRefreshTokenDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL).mutate<ResponseRefreshTokenDto>({
        mutation: refreshTokenMutation
      });
  }
}
