import { Component, Inject, Input, OnInit, PLATFORM_ID } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HeaderModalComponent } from '../../../../main/home/header-modal/header-modal.component';
import { ModalService } from '../../../service/modal/modal.service';
import { EmptyAddressModalComponent } from '../../../../main/home/empty-address-modal/empty-address-modal.component';
import { ListAddressModalComponent } from '../../../../main/home/list-address-modal/list-address-modal.component';
import { Location, isPlatformBrowser } from '@angular/common';
import { AuthData } from '../../../data/auth.data';
import { WindowActionService } from '../../../service/window-action.service';
import { CountInboxService } from '../../../service/count-inbox.service';
import { CountCartService } from '../../../service/count-cart.service';

@Component({
  selector: 'home-header',
  templateUrl: './home-header.component.html'
})
export class HomeHeaderComponent implements OnInit {
  cartItem = '0';
  coverageArea = 'Masukkan Alamat';
  showModal = false;
  token = '';
  isReady = false;

  @Input() search = '';
  @Input() showBackButton = false;

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private router: Router,
    private modalService: ModalService,
    private activatedRouted: ActivatedRoute,
    private location: Location,
    public authData: AuthData,
    private windowActionService: WindowActionService,
    public countInboxService: CountInboxService,
    public countCartService: CountCartService
  ) {}

  ngOnInit(): void {
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
    }
    if (this.activatedRouted.snapshot.data['canGoBack']) {
      this.showBackButton = this.activatedRouted.snapshot?.data['canGoBack'];
    }
    // this.coverageArea = this.authData.coverageArea;
    this.token = this.authData.token;
    if (this.token) {
      this.getDefaultAddress();
      this.countCartService.getCountCart();
      this.countInboxService.getCountInbox();
    }

    if (Object.keys(this.authData.location).length) {
      this.coverageArea =
        this.authData.location.district.name +
        ',' +
        this.authData.location.regency.name;
    }
    
  }

  getDefaultAddress() {
    // this.addressService.defaultAddress().subscribe({
    //   next: ({ data }) => {
    //     if (data) {
    //       this.authData.village = data.defaultAddress.village;
    //       this.authData.coverageArea = `${data.defaultAddress.district.name}, ${data.defaultAddress.regency.name}`;
    //       this.authData.isDefaultAddress = true;
    //     } else {
    //       this.emptyAddressModal();
    //     }
    //   },
    //   error: (err) => {
    //     err;
    //   }
    // });
  }

  toSearchPage() {
    this.router.navigate(['/search'], {
      queryParams: { search: this.search }
    });
  }


  toLoginPage() {
    this.router.navigate(['/login'], {});
  }

  toCart() {
    this.router.navigate(['/cart/list'], {});
  }

  toInbox() {
    this.router.navigate(['/inbox'], {});
  }

  toProfile() {
    this.router.navigate(['/profile/dashboard'], {});
  }

  setLocation() {
    if (this.token) {
      // const isDefaultAddress = this.authData.isDefaultAddress;
      // this.listAddressModal();
      // if (isDefaultAddress) {
      this.router.navigate(["/address/list"]);
    } else {
      this.headerModal();
    }
  }

  headerModal() {
    const modalRef = this.modalService.open(HeaderModalComponent, {
      title: 'Kirim belanja stok kemana?',
      closeButton: true
    });
    modalRef.onResult().subscribe((closed) => {
      if (closed === 'location') {
        this.router.navigate(['/location/set'], {});
      } else if (closed === 'login') {
        this.router.navigate(['/login']);
      }
    });
  }

  emptyAddressModal() {
    const modalRef = this.modalService.open(EmptyAddressModalComponent);
    modalRef.onResult().subscribe((button) => {
      switch (button) {
        case 'add':
          this.router.navigate(['/address/add'], {});
          break;
        case 'skip':
          break;
        default:
          break;
      }
    });
  }

  listAddressModal() {
    const modalRef = this.modalService.open(ListAddressModalComponent);
    modalRef.onResult().subscribe((button) => {
      switch (button) {
        case 'use':
          setTimeout(() => {
            this.windowActionService.reload();
          }, 500);
          break;
        case 'add':
          this.router.navigate(['/address/add'], {});
          break;
        default:
          break;
      }
    });
  }

  goBack() {
    if (window.history.length < 3) {
      this.router.navigate(['/']);
    } else {
      this.location.back();
    }
  }
}
