import { Injectable } from '@angular/core';
import { Apollo, MutationResult } from 'apollo-angular';
import { CreateRequestOtpDto, ResponseRequestOtptDto } from './otp.dto';
import { Observable } from 'rxjs';
import { createRequestOtp } from './otp.constant';
import { ListApiTypes } from '../../../types/list-api.types';

@Injectable({
  providedIn: 'root'
})
export class OtpService {
  constructor(private apollo: Apollo) {}

  requestOtpMutation(
    data: CreateRequestOtpDto
  ): Observable<MutationResult<ResponseRequestOtptDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseRequestOtptDto>({
        mutation: createRequestOtp,
        variables: data
      });
  }
}
