import {gql} from "apollo-angular";

export const exampleCreateTransaction = gql`
  mutation createTransaction($createTransactionInput: CreateTransactionInput!) {
    createTransaction (input: $createTransactionInput){
      id
      company{
        id
        name
        logo
        country{
          id
          name
          code
        }
      }
      referenceId
      amount
      description
      dueDate
      expiredAt
      currency
      status
      category
      type
      createdAt
      updatedAt
    }
  }
`;
