import { Injectable } from "@angular/core";
import { ApolloQueryResult } from "@apollo/client";
import { ListApiTypes } from '../../../types/list-api.types';
import { Apollo } from "apollo-angular";
import { Observable } from "rxjs";

import {
  detailWarehouseBySupplierInputDto,
  DetailWarehouseInput,
  ListWarehouseDto,
  ResponseDetailWarehouseDto,
  ResponseGetWarehouseDto,
  ResponseListWarehouseDto
} from "./warehouse.dto";
import {detailWarehouse, getWarehouse, listWarehouse} from "./warehouse.constant";

@Injectable({
    providedIn: 'root'
})
export class WarehouseService{
  constructor(private apollo: Apollo){}

  getWarehouse(data:detailWarehouseBySupplierInputDto):Observable<ApolloQueryResult<ResponseGetWarehouseDto>>{
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseGetWarehouseDto>({
      query: getWarehouse,
      variables: data
    });
  }

  listWarehouse(data: ListWarehouseDto): Observable<ApolloQueryResult<ResponseListWarehouseDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseListWarehouseDto>({
      query: listWarehouse,
      variables: data
    })
  }

  detailWarehouse(data: DetailWarehouseInput): Observable<ApolloQueryResult<ResponseDetailWarehouseDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseDetailWarehouseDto>({
      fetchPolicy: 'no-cache',
      query: detailWarehouse,
      variables: data
    })
  }
}
