import { Pipe, PipeTransform } from "@angular/core";
import * as dayjs from "dayjs";
import "dayjs/locale/id";

dayjs.locale("id");


export interface OperationalHours {
  day: number;
  openTime: string;
  closeTime: string;
  isOpen: boolean;
}

@Pipe({
  name: "timeOperationalPipe"
})
export class TimeOperationalPipe implements PipeTransform {

  transform(value: OperationalHours, timeLabel?: string): string {
    if (value && value.isOpen) {
      return this.formatDate(value.openTime) + ' - ' + this.formatDate(value.closeTime) + " " + (timeLabel ? timeLabel : "");
    } else {
      return "tutup";
    }
  }


  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  formatDate(time: any) {
    const [hours, minutes, seconds] = time.split(":");
    return dayjs()
      .set("hours", hours)
      .set("minutes", minutes)
      .set("seconds", seconds)
      .format("HH:mm");
  };

}
