import { Component } from '@angular/core';
import { Modal } from '../../../shared/service/modal/modal.model';

@Component({
  selector: 'empty-address-modal',
  templateUrl: './empty-address-modal.component.html'
})
export class EmptyAddressModalComponent extends Modal {
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  onInjectInputs(): void {}

  action(value: string): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({});
  }
}
