import { Injectable } from '@angular/core';
import { Apollo } from 'apollo-angular';
import {
  DetailPaymentInput,
  ResponseDetailPayment,
} from './payment.dto';
import { Observable } from 'rxjs';
import { ApolloQueryResult } from '@apollo/client';
import { ListApiTypes } from '../../../types/list-api.types';
import { detailPayment } from './payment.constant';

@Injectable({
  providedIn: 'root'
})
export class BOSPaymentService {
  constructor(private apollo: Apollo) {}

  detailPayment(
    data: DetailPaymentInput
  ): Observable<ApolloQueryResult<ResponseDetailPayment>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseDetailPayment>({
        query: detailPayment,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }
}
