import {
  ApplicationRef,
  ComponentFactory,
  ComponentFactoryResolver,
  Injectable,
  Type
} from '@angular/core';
import { Modal } from './modal.model';
import { ModalRef } from './modal-ref.model';
import { BottomModalComponent } from '../../components/modal/bottom-modal/bottom-modal.component';
import { ModalHeaderComponent } from '../../components/modal/modal-header/modal-header.component';

export interface InputModalDto {
  title: string;
  height?: string;
  closeButton: boolean;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  data?: any;
}

@Injectable({
  providedIn: 'root'
})
export class ModalService {
  private modalContainer!: HTMLElement;
  private modalContainerFactory!: ComponentFactory<BottomModalComponent>;
  private modalHeaderFactory!: ComponentFactory<ModalHeaderComponent>;

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private appRef: ApplicationRef
  ) {
    this.setupModalContainerFactory();
  }

  open<T extends Modal>(component: Type<T>, inputs?: InputModalDto): ModalRef {
    this.setupModalContainerDiv();
    const modalContainerRef = this.appRef.bootstrap(
      this.modalContainerFactory,
      this.modalContainer
    );
    const modalComponentRef = modalContainerRef.instance.createModal(
      component,
      inputs
    );
    const modalHeaderRef =
      modalContainerRef.instance.createHeader(ModalHeaderComponent);
    if (inputs) {
      modalComponentRef.instance.onInjectInputs(inputs);
      modalHeaderRef.instance.onInjectInputs(inputs);
    }
    const modalRef = new ModalRef(
      modalContainerRef,
      modalComponentRef,
      modalHeaderRef
    );
    return modalRef;
  }

  private setupModalContainerDiv(): void {
    this.modalContainer = document.createElement('div');
    document.getElementsByTagName('body')[0].appendChild(this.modalContainer);
  }

  private setupModalContainerFactory(): void {
    this.modalContainerFactory =
      this.componentFactoryResolver.resolveComponentFactory(
        BottomModalComponent
      );
    this.modalHeaderFactory =
      this.componentFactoryResolver.resolveComponentFactory(
        ModalHeaderComponent
      );
  }
}
