import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'lib-btok-ajak-cuan-card',
  templateUrl: './btok-ajak-cuan-card.component.html',
  styleUrl: './btok-ajak-cuan-card.component.scss'
})
export class BtokAjakCuanCardComponent {
  @Output() showCopyModal = new EventEmitter<boolean>();
  @Output() goDetailProduct = new EventEmitter<void>();
  @Input() supplierName = 'Youtap Official Store';
  @Input() variant = 'Paket Usaha Gerobak Sari Roti';
  @Input() productName = '';
  @Input() price = 2500000;
  @Input() comision = 0;
  @Input() time: string | Date = 'Tanpa Batasan';
  @Input() image = 'https://ds393qgzrxwzn.cloudfront.net/resize/m640x640/cat1/img/images/0/xNsp9DPEtQ.jpg';
  @Input() priceType = 'ITEM_PRICE_TYPE_FIXED';
  @Input() stock = 0;
  @Input() tierImage = '';

  onCopyModal() {
    this.showCopyModal.emit();
  }

  handleDetailProduct() {
    this.goDetailProduct.emit()
  }
}
