import { Pipe, PipeTransform } from "@angular/core";
import {PaymentStatusTypes} from "../types/payment-status.types";

@Pipe({
  name: "transactionStatusPipe"
})
export class TransactionStatusPipe implements PipeTransform {

  transform(status: string): string {
    switch (status) {
      case PaymentStatusTypes.SUCCESS:
        return 'Selesai';
      case PaymentStatusTypes.FAILED:
        return 'Tidak Berhasil';
      default:
        return 'Menunggu Pembayaran';
    }
  }

}
