/* eslint-disable @typescript-eslint/no-explicit-any */
import { Injectable } from "@angular/core";
import { Resolve } from "@angular/router";
import { Observable } from "rxjs";
import { AuthData } from "../data/auth.data";
import {ProfileService} from "@youtap-web-lib";
import {GaService} from "../service/ga.service";

@Injectable({ providedIn: 'root' })
export class ProfileResolver implements Resolve<boolean> {

  constructor(
    private profileService: ProfileService,
    private authData: AuthData,
    private gaService: GaService
  ) {}

  resolve(): Observable<boolean> | Promise<boolean> | boolean {
    if(this.authData.token){
      return this.profileService.getProfile().toPromise().then(({ data }: any) => {
        this.authData.user = data.detailUser;
        this.gaService.setUserId()
        return true;
      }).catch(() => {
        return false;
      });
    }else {
      return true
    }
  }
}
