import { Injectable } from "@angular/core";
import { Resolve } from "@angular/router";
import { Observable } from "rxjs";
import { AuthData } from "../data/auth.data";
import {AddressService} from "@youtap-web-lib";

@Injectable({ providedIn: 'root' })
export class AddressResolver implements Resolve<boolean> {

  constructor(
    private addressService: AddressService,
    private authData: AuthData
  ) {}

  resolve(): Observable<boolean> | Promise<boolean> | boolean {
      // debugger
    if(this.authData.token){
      return this.addressService.defaultAddress().toPromise().then(({ data }: any) => {
        this.authData.location = data.defaultAddress;
        return true;
      }).catch(() => {
        return false;
      });
    }else {
      return true
    }
  }
}
