import { Directive, ElementRef, Renderer2, AfterViewInit, Input, Inject, PLATFORM_ID } from "@angular/core";
import { isPlatformBrowser } from "@angular/common";

@Directive({
  selector: "[lib-mandatory-label]"
})
export class MandatoryLabelDirective implements AfterViewInit {
  @Input() mandatory = true;
  isReady = false
  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private el: ElementRef,
    private render: Renderer2
  ) {
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
    }
  }

  ngAfterViewInit() {
    if (this.mandatory && this.isReady) {
      const span = document.createElement("span");
      span.innerHTML = " <small>*</small>";
      span.style.color = "red";
      this.render.appendChild(this.el.nativeElement, span);
    }
  }
}
