import {gql} from "apollo-angular";

export const createAgent = gql`
  mutation ($createAgentInput: CreateAgentInput!) {
    createAgent(createAgentInput: $createAgentInput) {
      referralCode
      status
      userId
    }
  }
`;

export const checkKycAgent = gql`
  query {
    checkKycAgent {
      statusKyc
    }
  }
`;


export const listItemAjakCuanSCM = gql`
  query listItem($listItemInput: ListItemInput!){
  listItem(listItemInput: $listItemInput){
    page
    limit
    total
    totalPage
    items {
      amount
      fundingSource
      id
      maxPrice
      periodStart
      periodEnd
      priceType
      planOption{
        id
        price
        term
        termType
        type
        image
        plan {
          name
          displayName
        }
      }
    }
  }
}
`;

export const pinAuthorization = gql`
  mutation ($pinAuthorizationInput: PinAuthorizationInput!) {
    pinAuthorization(pinAuthorizationInput: $pinAuthorizationInput) {
      otpExpiredAt
      authToken {
        accessToken
      }
    }
  }
`;

