import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'lib-basic-product-card',
  templateUrl: './basic-product-card.component.html',
  styleUrls: ['./basic-product-card.component.scss']
})
export class BasicProductCardComponent {
  @Input() image =
    'https://ds393qgzrxwzn.cloudfront.net/resize/m640x640/cat1/img/images/0/xNsp9DPEtQ.jpg';
  @Input() productName = 'Paprika Kuning dan Paprika Hijau dan Paprika Merah';
  @Input() productVariantName = 'Sayuran';
  @Input() price = 10000;
  @Input() priceBeforeDiscount = 11000;
  @Input() unit = 'unit';
  @Input() discountValue = 1000;
  @Input() discountType = 'FIXED';
  @Input() stock = 10;
  @Input() maxLength = 30;
  @Input() isPOD = false;
  @Input() isReferral = false;
  @Input() promo = false;
  @Input() official_supplier: string = '';
  @Output() clickProduct: EventEmitter<void> = new EventEmitter();
  
  isErrorImage = false;
  initialName = '';

  onClickProduct() {
    this.clickProduct.emit();
  }

  handleImageError(event: Event, name: string): void {
    this.isErrorImage = true;
    this.initialName = this.initials(name);
  }

  initials(value: string): string {
    const fullName = value.split(" ");
    const first = fullName.shift();
    const last = fullName.pop();
    const lastInit = last ? last?.charAt(0) : "T";
    const firstInit = first ? first.charAt(0) : "Y";
    return firstInit + lastInit;
    // const data = value ? value : 'YT';
    // return data.match(/(\b\S)?/g).join("").match(/(^\S|\S$)?/g).join("").toUpperCase();
  }
}
