import { Component, EventEmitter, Input, OnChanges, Output } from '@angular/core';
import {DomSanitizer, SafeHtml} from "@angular/platform-browser";

@Component({
  selector: 'lib-basic-empty-state',
  templateUrl: './basic-empty-state.component.html'
})
export class BasicEmptyStateComponent implements OnChanges {
  @Input() image = 'default.png';
  @Input() label = 'label';
  @Input() description = 'description';
  @Input() buttonLabel = 'button';
  @Input() buttonWidth = '!w-full'
  @Input() buttonLabelSecondary = 'button';
  @Input() showButton = false;
  @Input() showButtonSecondary = false;

  @Output() clickButton = new EventEmitter<string>();
  @Output() clickButtonSecondary = new EventEmitter<string>();

  sanitizedDescription: SafeHtml

  constructor(private sanitizer: DomSanitizer) {
    this.sanitizedDescription = '';
  }

  ngOnChanges(): void {
    this.sanitizedDescription = this.sanitizer.bypassSecurityTrustHtml(this.description);
  }

  handleClick() {
    this.clickButton.emit();
  }

  handleClickSecondary() {
    this.clickButtonSecondary.emit();
  }
}
