import { Injectable } from '@angular/core';
import { Apollo } from 'apollo-angular';
import { Observable } from 'rxjs';
import { IResponseListBannerDto, RequestListBannerDto, ResponseListBannerConfigurationDto, ListBannerConfigurationInputDto } from './banner.dto';
import { ApolloQueryResult } from '@apollo/client';
import { ListApiTypes } from '../../../types/list-api.types';
import { listBanner, listBannerConfiguration } from './banner.constant';

@Injectable({
  providedIn: 'root'
})
export class BannerService {
  constructor(private apollo: Apollo) {
  }

  listBanner(data: RequestListBannerDto): Observable<ApolloQueryResult<IResponseListBannerDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<IResponseListBannerDto>({
        query: listBanner,
        fetchPolicy: 'no-cache',
        variables: data
      });
  }

  listBannerConfiguration(data: ListBannerConfigurationInputDto): Observable<ApolloQueryResult<ResponseListBannerConfigurationDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListBannerConfigurationDto>({
        query: listBannerConfiguration,
        fetchPolicy: 'no-cache',
        variables: data
      })
  }
}
