import { Component, Input } from '@angular/core';

export type ColorTypeDto =
  | 'green'
  | 'red'
  | 'blue'
  | 'yellow'
  | 'pink'
  | 'grey'
  | 'orange'
  | 'orange2'
  | 'red2';

@Component({
  selector: 'lib-surface-badge',
  templateUrl: './surface-badge.component.html'
})
export class SurfaceBadgeComponent {
  textColor = '';
  backgroundColor = '';
  @Input() label = '';
  @Input() useBackground = true;
  @Input() fontSize = 10;
  @Input() padding = 'p-[2px]';
  @Input() width = 'w-fit';
  @Input() image = '';
  @Input() quantity!: number;
  @Input() set color(data: string) {
    this.textColor = this.setTextColor(data);
    if (this.useBackground) {
      this.backgroundColor = this.setBgColor(data);
    }
  }

  setTextColor(color: string): string {
    switch (color) {
      case 'green':
        return 'text-secondary-green-2';
      case 'red':
        return 'text-secondary-red-2';
      case 'blue':
        return 'text-[#1CAED4]';
      case 'yellow':
        return 'text-[#F2C94C]';
      case 'pink':
        return 'text-[#FF0044]';
      case 'grey':
        return 'text-[#828282]';
      case 'orange':
        return 'text-primary-yt-orange';
      case 'orange2':
        return 'text-white';
      case 'red2':
        return 'text-white';
      default:
        return 'text-secondary-green-2';
    }
  }

  setBgColor(color: string): string {
    switch (color) {
      case 'green':
        return 'bg-[#E6FFDB]';
      case 'red':
        return 'bg-[#FFD9D9]';
      case 'blue':
        return 'bg-[#E9F7FB]';
      case 'yellow':
        return 'bg-[#FEF7E2]';
      case 'pink':
        return 'bg-[#FFD9D9]';
      case 'grey':
        return 'bg-[#F2F2F2]';
      case 'orange':
        return 'bg-secondary-orange-light';
      case 'orange2':
        return 'bg-[#FF751D]';
      case 'red2':
        return 'bg-[#e40000]';
      default:
        return `bg-[#E6FFDB]`;
    }
  }
}
