import {Component, EventEmitter, Input, Output} from '@angular/core';

@Component({
  selector: 'lib-share-link-card',
  templateUrl: './share-link-card.component.html'
})
export class ShareLinkCardComponent {
  @Input() image = "";
  @Input() name = "";
  @Output() clickLink: EventEmitter<void> = new EventEmitter();
  @Input() href = "";

  onClickLink() {
    this.clickLink.emit();
  }
}
