import { gql } from "apollo-angular";

export const detailAgent = gql`
  query detailAgent {
    detailAgent {
      id
      userId
      parentId
      referralCode
      status
    }
  }
`;

export const listItem = gql`
  query listItem($listItemInput: ListItemInput!) {
    listItem(listItemInput: $listItemInput) {
      page
      totalPage
      total
      limit
      items {
        id
        type
        amount
        periodEnd
        periodStart
        maxPrice
        priceType
        productVariant {
          id
          name
          price
          slug
          stock
          productVariantWarehouses {
            status
          }
          product {
            id
            slug
            name
            image
            supplier {
              name
              status
              tier {
                image
              }
            }
          }
        }
        supplier {
          id
          name
          image
          slug
          tier {
            name
            image
          }
        }
      }
    }
  }
`;

export const generateLinkAgentItem = gql`
  mutation generateLinkAgentItem($generateLinkAgentItemInput: CreateAgentItemInput!){
  generateLinkAgentItem(generateLinkAgentItemInput : $generateLinkAgentItemInput) {
    id
    code
    path
  }
}
`;
