/* eslint-disable @typescript-eslint/no-explicit-any */
import {
  AfterViewInit,
  Component,
  Inject,
  OnInit,
  PLATFORM_ID
} from '@angular/core';
import {
  ActivatedRoute,
  NavigationCancel,
  NavigationEnd,
  NavigationError,
  NavigationStart,
  Router
} from '@angular/router';
import { LocationService } from './shared/service/location.service';
import { AuthData } from './shared/data/auth.data';
import { getDetailLocationDto } from '@youtap-web-lib';
import { isPlatformBrowser } from '@angular/common';
import { MetaService } from './shared/service/meta.service';
import { filter } from 'rxjs';
import { PlatformTypeService } from './shared/service/platform-type.service';
import { WindowActionService } from './shared/service/window-action.service';
@Component({
  selector: 'youtap-bos-root',
  templateUrl: './app.component.html'
})
export class AppComponent implements AfterViewInit, OnInit {
  title = 'youtap-bos';
  loading = true;
  isAppBosModal = false;
  isReady = false;

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private router: Router,
    private locationService: LocationService,
    public authData: AuthData,
    private metaService: MetaService,
    private activatedRoute: ActivatedRoute,
    private platformTypeService: PlatformTypeService,
    private windowActionService: WindowActionService
  ) {
    this.router.events
      .pipe(filter((event) => event instanceof NavigationEnd))
      .subscribe(() => {
        const rt = this.getChild(this.activatedRoute);
        rt.data.subscribe(({ meta }: any) => {
          if (meta) {
            this.metaService.setMeta(meta);
          }
        });
      });
  }

  getChild(activatedRoute: ActivatedRoute): any {
    if (activatedRoute.firstChild) {
      return this.getChild(activatedRoute.firstChild);
    } else {
      return activatedRoute;
    }
  }
  ngOnInit() {
    setTimeout(() => {
      this.loading = false;
    }, 300);
  }

  ngAfterViewInit() {
    this.checkLocation();
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
      if (
        this.platformTypeService.checkIsTablet() ||
        this.platformTypeService.checkIsMobile()
      ) {
        const pathName = this.windowActionService.getPathName();
        if(pathName !== '/campaign') {
          this.appBosModal();
        }
      }
    }
  }

  checkRoute() {
    this.router.events.subscribe((e) => {
      if (e instanceof NavigationStart) {
        this.loading = true;
      }
      if (e instanceof NavigationEnd) {
        this.loading = false;
      }
      // Set loading state to false in both of the below events to hide the spinner in case a request fails
      if (e instanceof NavigationCancel) {
        this.loading = false;
      }
      if (e instanceof NavigationError) {
        this.loading = false;
      }
    });
  }

  checkLocation() {
    const village = this.authData.location;
    if (!Object.keys(village).length) {
      this.locationService
        .getPosition()
        .then(() => {
          this.locationService.setLocation();
        })
        .catch(() => {
          const detailLocation: getDetailLocationDto = {
            detailLocationInput: {
              latitude: this.authData.defaultPosition.latitude,
              longitude: this.authData.defaultPosition.longitude
            }
          };
          this.locationService.detailLocation(detailLocation);
        });
    }
  }

  appBosModal() {
    setTimeout(() => {
      const firstOpen = localStorage.getItem('firstOpen');
      const pageReloaded = window.performance
        .getEntriesByType('navigation')
        .map((nav) => (nav as any).type)
        .includes('reload');
      if (!pageReloaded) {
        this.isAppBosModal = true;
      } else {
        if (!firstOpen) {
          this.isAppBosModal = true;
        }
      }
    }, 3000)
  }

  closeBosModal() {
    this.isAppBosModal = false;
    localStorage.setItem('firstOpen', 'true');
  }
}
