import { NgModule } from '@angular/core';
import {
  ApolloModule,
  APOLLO_NAMED_OPTIONS,
  NamedOptions
} from 'apollo-angular';
import { ApolloLink, InMemoryCache } from '@apollo/client/core';
import { HttpLink } from 'apollo-angular/http';
import { onError } from '@apollo/client/link/error';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { AuthInterceptors } from './auth.interceptors';
import { Router } from '@angular/router';

const {
  SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL: supplyChainPublicApi,
  BOS_API_GRAPHQL_URL: bosApi,
  POS_API_GRAPHQL_URL: posApi
} = environment;

export function createApollo(httpLink: HttpLink, router: Router): NamedOptions {
  // const village: Village = authData.village ? authData.village : authData.defaultVillage;
  // const token = authData.token;
  // const authLink = new ApolloLink((operation, forward) => {
  //   if (token) {
  //     operation.setContext({
  //       headers: {
  //         Authorization: token
  //       }
  //     });
  //   } else {
  //     operation.setContext({
  //       headers: {
  //         'x-village-id': village.id
  //       }
  //     });
  //   }
  //   return forward(operation);
  // });

  const errorLink = onError(({ graphQLErrors }) => {
    if (graphQLErrors) {
      for (const err of graphQLErrors) {
        if (
          err.extensions['code'] &&
          err.extensions['code'] == 'UNAUTHENTICATED'
        ) {
          localStorage.clear();
          router.navigate(['/login']);
        }
        // if(err.extensions["code"] === "INTERNAL_SERVER_ERROR") {
        //   router.navigate(["/error/undefined"]);
        // }
      }
    }
  });

  const httpLinkWithErrorHandling = ApolloLink.from([errorLink]);
  return {
    supplyChainPublicApi:
      /* <-- These settings will be saved by name: newClientName */ {
        cache: new InMemoryCache({
          addTypename: false
        }),
        link: ApolloLink.concat(
          httpLinkWithErrorHandling,
          httpLink.create({
            uri: supplyChainPublicApi
          })
        )
      },
    bosApi: /* <-- These settings will be saved by name: newClientName */ {
      cache: new InMemoryCache({
        addTypename: false
      }),
      link: ApolloLink.concat(
        httpLinkWithErrorHandling,
        httpLink.create({
          uri: bosApi
        })
      )
    },
    posApi: /* <-- These settings will be saved by name: newClientName */ {
      cache: new InMemoryCache({
        addTypename: false
      }),
      link: ApolloLink.concat(
        httpLinkWithErrorHandling,
        httpLink.create({
          uri: posApi + '/graphql'
        })
      )
    }
  };
}

@NgModule({
  exports: [ApolloModule, HttpClientModule],
  imports: [],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptors,
      multi: true
    },
    {
      provide: APOLLO_NAMED_OPTIONS,
      useFactory: createApollo,
      deps: [HttpLink, Router]
    }
  ]
})
export class GraphQLModule {}
