/* eslint-disable @typescript-eslint/no-explicit-any */
export class ValidationService {
  static getValidatorErrorMessage(
    validatorName: string,
    validatorValue?: any,
    label?: string,
    custom?: string
  ) {
    const config = {
      required: `${label} wajib diisi.`,
      email: "Email tidak valid.",
      emailcomplete: "Email tidak valid.",
      min: `Minimal value dari ${label ? label.toLowerCase() : ""} adalah ${
        validatorValue.min
      }.`,
      max: `Value maximal dari ${label ? label.toLowerCase() : ""} adalah ${
        validatorValue.max
      }.`,
      minlength: `${label ? label : ""} harus lebih dari ${validatorValue.requiredLength} karakter.`,
      maxlength: `${label ? label : ""} harus kurang dari ${validatorValue.requiredLength} karakter.`,
      alphanumaddr: `${label ? label : ""} harus alphabets, numeric atau character(- . /).`,
      alphanum: `${label ? label : ""} harus alphabets atau numeric.`,
      alpha: `${label ? label : ""} harus alphabets.`,
      num: `${label ? label : ""} harus angka.`,
      fax: `Please enter a valid fax number.`,
      slug: `Bad slug code, good example "this-is-good-slug"`,
      phone: `Masukkan nomor HP yang valid`,
      noSymbol: `Tidak boleh memakai spesial karakter. misal: '+', '-', '$'`,
      url: `Url yang dimasukan tidak valid`,
    };

    if (custom) {
      return custom;
    } else {
      // @ts-ignore
      const errorMessage: string = config[validatorName] ? config[validatorName] : `${label} invalid`;
      return errorMessage;
    }
  }

  static emailCompleteValidator(input: any) {
    const regex = new RegExp(
      /^([a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,})*$/
    );
    if (regex.test(input.value)) {
      return null;
    } else {
      return { emailcomplete: true };
    }
  }

  static numValidator(input: any) {
    const regex = new RegExp(/^[0-9]*$/);
    if (regex.test(input.value)) {
      return null;
    } else {
      return { num: true };
    }
  }

  static alphaNumAddressValidator(input: any) {
    const regex = new RegExp(/^[a-zA-Z0-9-.\/]+( [a-zA-Z0-9-.\/]+)*$/);
    if (regex.test(input.value)) {
      return null;
    } else {
      return { alphanumaddr: true };
    }
  }

  static alphaNumValidator(input: any) {
    const regex = new RegExp(/^[a-zA-Z0-9]+( [a-zA-Z0-9]+)*$/);
    if (regex.test(input.value)) {
      return null;
    } else {
      return { alphanum: true };
    }
  }

  static alphaValidator(input: any) {
    const regex = new RegExp(/^[a-zA-Z ]+( [a-zA-Z ]+)*$/);
    if (regex.test(input.value)) {
      return null;
    } else {
      return { alpha: true };
    }
  }

  static faxValidator(input: any) {
    const regex = new RegExp(
      /^(\+?\d{1,}(\-?)\d*(\-?)\(?\d{2,}\)?(\-?)\d{3,}\d{3,})*$/
    );
    if (regex.test(input.value)) {
      return null;
    } else {
      return { fax: true };
    }
  }

  static slugValidator(input: any) {
    const regex = new RegExp(/^[a-z0-9]+(?:-[a-z0-9]+)*$/);
    if (regex.test(input.value)) {
      return null;
    } else {
      return { slug: true };
    }
  }
  static noSymbolValidator(input: any) {
    const regex = new RegExp(/^[a-z0-9]+(?:-[a-z0-9]+)*$/);
    if (regex.test(input.value)) {
      return null;
    } else {
      return { noSymbol: true };
    }
  }
  static phoneValidator(input: any) {
    const regex = new RegExp(/^(62|0)8[1-9][0-9]{6,12}$/);
    if (regex.test(input.value)) {
      return null;
    } else {
      return { phone: true };
    }
  }

  static urlValidator(input: any) {
    const regex = new RegExp(/^(https?:\/\/)?([\da-z.-]+)\.([a-z.]{2,6})([/\w.-]*)*\/?$/);
    if (regex.test(input.value)) {
      return null;
    } else {
      return { url: true };
    }
  }
}
