import { APP_INITIALIZER, NgModule } from '@angular/core';
import {
  BrowserModule,
  provideClientHydration
} from '@angular/platform-browser';
import { AppComponent } from './app.component';
import { RouterModule } from '@angular/router';
import { appRoutes } from './app.routes';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {
  HttpClientJsonpModule,
  HttpClientModule,
  provideHttpClient,
  withFetch
} from '@angular/common/http';
import { GraphQLModule } from './graphql-config/graphql.module';
import { CountInboxService } from './shared/service/count-inbox.service';
import { AuthData } from './shared/data/auth.data';
import { CountCartService } from './shared/service/count-cart.service';
import {
  NgxGoogleAnalyticsModule,
  NgxGoogleAnalyticsRouterModule
} from 'ngx-google-analytics';
import { environment } from '../environments/environment';
// import { GoogleTagManagerModule } from 'angular-google-tag-manager';
import { InputsModule } from './shared/components/inputs/inputs.module';
import { ModalModule } from '@youtap-web-lib';
import { ModalModule as ModalMainModule } from './main/modal/modal.module';
import { ReferralService } from './shared/service/referral.service';
import { ContainerModule } from './shared/layout/container/container.module';

@NgModule({
  declarations: [AppComponent],
  imports: [
    BrowserModule.withServerTransition({ appId: 'serverApp' }),
    BrowserModule,
    BrowserAnimationsModule,
    RouterModule.forRoot(appRoutes, { initialNavigation: 'enabledBlocking' }),
    HttpClientModule,
    HttpClientJsonpModule,
    GraphQLModule,
    // GoogleTagManagerModule.forRoot({ id: environment.GOOGLE_TAG_MANAGER }),
    NgxGoogleAnalyticsModule.forRoot(environment.GOOGLE_ANALYTICS),
    NgxGoogleAnalyticsRouterModule,
    InputsModule,
    ModalModule,
    ModalMainModule,
    ContainerModule
  ],
  providers: [
    {
      provide: APP_INITIALIZER,
      useFactory: (service: CountInboxService) => () => {
        service.getCountInbox();
      },
      deps: [CountInboxService, AuthData],
      multi: true
    },
    {
      provide: APP_INITIALIZER,
      useFactory: (service: CountCartService) => () => {
        service.getCountCart();
      },
      deps: [CountCartService, AuthData],
      multi: true
    },
    {
      provide: APP_INITIALIZER,
      useFactory: (referral: ReferralService) => () => {
        referral.validateReferral();
      },
      deps: [ReferralService],
      multi: true
    },
    // { provide: 'googleTagManagerId', useValue: environment.GOOGLE_TAG_MANAGER },
    provideClientHydration(),
    provideHttpClient(withFetch())
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}
