import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'lib-contact-card',
  templateUrl: './contact-card.component.html'
})
export class ContactCardComponent {
  @Input() image = '';
  @Input() label = '';
  @Input() email = '';
  @Input() whatsapp = '';
  @Output() openEmail: EventEmitter<string> = new EventEmitter();
  @Output() openWhatsapp: EventEmitter<string> = new EventEmitter();

  clickEmail() {
    // this.windowActionService.href('mailto:' + this.email)
    this.openEmail.emit('mailto:' + this.email);
  }

  clickWhatsapp() {
    // this.windowActionService.open('https://api.whatsapp.com/send?phone=' + this.whatsapp.replace(/\s/g, ""), '_blank')
    this.openWhatsapp.emit(
      'https://api.whatsapp.com/send?phone=' + this.whatsapp.replace(/\s/g, '')
    );
  }
}
