import { Component, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'lib-subs-ajak-cuan-card',
  templateUrl: './subs-ajak-cuan-card.component.html',
  styleUrl: './subs-ajak-cuan-card.component.scss'
})
export class LibSubsAjakCuanCardComponent {
  @Output() action = new EventEmitter<void>();
  @Input() image: string = './assets/images/png/paket extra mantap.png';
  @Input() title: string = 'Youtap POS';
  @Input() term: number = 1;
  @Input() termType: string = 'Tahun';
  @Input() background: string = 'bg-white';
  @Input() description: string = 'Gratis';
  @Input() infoLimit: boolean = false;
  @Input() label: string = 'Bagikan';
  @Input() timeLimit: string | Date = 'Tanpa Batasan';

  emitEvent() {
    this.action.emit();
  }
}
