import {Injectable} from "@angular/core";
import {GoogleAnalyticsService} from "ngx-google-analytics";
import {WindowActionService} from "./window-action.service";
import {AuthData} from "../data/auth.data";


@Injectable({
  providedIn: "root"
})
export class GaService {

  constructor(
    private googleAnalyticsService: GoogleAnalyticsService,
    private windowActionService: WindowActionService,
    private authData: AuthData
  ) {
  }

  addEvent(eventName: string, category: string, name: string, mobileApp = false) {
    let userID = ''
    if (this.authData.user) {
      userID = this.authData.user.merchant.accountId
      this.setUserId()
    }
    this.googleAnalyticsService.event(eventName.toLowerCase(), category.toLowerCase(), userID);
    if (mobileApp) {
      this.windowActionService.dispatchEvent('tracker', {
        name: eventName,
        category: category
      })
    }
  }


  setUserId() {
    let userID = ''
    if (this.authData.user) {
      userID = this.authData.user.merchant.accountId
    }
    this.googleAnalyticsService.set({'userId': userID})
  }
}
