/* eslint-disable @typescript-eslint/no-explicit-any */
import {Component, OnInit} from '@angular/core';
import {Title} from '@angular/platform-browser';
import {ActivatedRoute, NavigationEnd, Router} from '@angular/router';

@Component({
  selector: 'youtap-bos-basic-container',
  templateUrl: './basic-container.component.html'
})
export class BasicContainerComponent implements OnInit {
  titlePage = 'title';
  header = true

  constructor(
    private title: Title,
    private route: Router,
    private activatedRoute: ActivatedRoute) {
  }

  ngOnInit(): void {
    this.getEventRoute()
    this.getTitle()
  }

  getEventRoute() {
    this.route.events.subscribe((data) => {
      if (data instanceof NavigationEnd) {
        setTimeout(() => {
          this.getTitle()
        }, 0);
      }
    });
  }

  getTitle() {
    this.setTitle();
    const rt = this.getChild(this.activatedRoute);
    rt.data.subscribe((val: any) => {
      if (val.pageTitle) {
        this.titlePage = val.pageTitle
      } else {
        this.setTitle()
      }

      if(typeof val.header === "boolean"){
        this.header = val.header
      }
    });
  }

  getChild(activatedRoute: ActivatedRoute): any {
    if (activatedRoute.firstChild) {
      return this.getChild(activatedRoute.firstChild);
    } else {
      return activatedRoute;
    }

  }

  setTitle() {
    this.titlePage = this.title.getTitle();
  }
}
