import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'initialName'
})
export class InitialNamePipe implements PipeTransform {

  transform(value: string): string {
    const fullName = value.split(' ');
    const first = fullName.shift();
    const last = fullName.pop();
    const lastInit = last ? last?.charAt(0) : '';
    const firstInit = first ? first.charAt(0) : '';
    return firstInit + lastInit;
    // const data = value ? value : 'YT';
    // return data.match(/(\b\S)?/g).join("").match(/(^\S|\S$)?/g).join("").toUpperCase();
  }

}
