import {Injectable} from '@angular/core';
import {Apollo} from 'apollo-angular';
import {
  getDetailLocationByVillageIdDto,
  getDetailLocationDto,
  RequestListSuggestionLocationDto,
  ResponseDetailLocationByVillageIdDto,
  ResponseDetailLocationDto,
  ResponseListSuggestionLocationDto
} from './location-api.dto';
import {ListApiTypes} from '../../../types/list-api.types';
import {Observable} from 'rxjs';
import {ApolloQueryResult} from '@apollo/client';
import {
  detailLocation,
  detailLocationByVillageId,
  lisSuggestionLocation
} from './location-api.constant';

@Injectable({
  providedIn: 'root'
})
export class LocationApiService {
  constructor(private apollo: Apollo) {
  }

  detailLocation(
    data: getDetailLocationDto
  ): Observable<ApolloQueryResult<ResponseDetailLocationDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseDetailLocationDto>({
        fetchPolicy: 'no-cache',
        query: detailLocation,
        variables: data
      });
  }

  lisSuggestionLocation(
    data: RequestListSuggestionLocationDto
  ): Observable<ApolloQueryResult<ResponseListSuggestionLocationDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListSuggestionLocationDto>({
        query: lisSuggestionLocation,
        variables: data
      });
  }

  detailLocationByVillageId(
    data: getDetailLocationByVillageIdDto
  ): Observable<ApolloQueryResult<ResponseDetailLocationByVillageIdDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseDetailLocationByVillageIdDto>({
        fetchPolicy: 'no-cache',
        query: detailLocationByVillageId,
        variables: data
      });
  }
}
