import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
  name: "errorMessageSplitPipe"
})

export class ErrorMessageSplitPipe implements PipeTransform {

  transform(value: string, character: string): string {
    if (value && character) {
      return value.split(character)[1] ;
    }else {
      return value
    }
  }

}
