import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'lib-range-date-card',
  templateUrl: './range-date-card.component.html',
  styleUrl: './range-date-card.component.scss'
})
export class RangeDateCardComponent {
  @Input() rangeDate: string = '3 Sep 2023 - 3 Okt 2023';
  @Input() label: string = 'Rentang waktu maks. 31 hari.';
  @Output() action = new EventEmitter<void>();

  emitEvent() {
    this.action.emit();
  }

}
