import { Injectable } from '@angular/core';
import { ApolloQueryResult } from '@apollo/client';
import { ListApiTypes } from '../../../types/list-api.types';
import { Apollo } from 'apollo-angular';
import { Observable } from 'rxjs';
import { listBank } from './bank.constant';
import { ListBankDto, ResponseListBankDto } from './bank.dto';

@Injectable({
  providedIn: 'root'
})
export class BankService {
  constructor(private apollo: Apollo) {}

  listBank(
    data: ListBankDto
  ): Observable<ApolloQueryResult<ResponseListBankDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseListBankDto>({
        query: listBank,
        variables: data
      });
  }
}
