import { Injectable } from '@angular/core';
import { Apollo, MutationResult } from 'apollo-angular';
import {
  CompleteTransactionInput,
  CreateTransactionInput,
  DetailTransactionSupplierInput,
  ListTransactionSupplierInputDto, ResponseCheckStatusTransactionSupplierDto,
  ResponseCompleteTransactionDto,
  ResponseCreateTransactionDto,
  ResponseDetailTransactionSupplierDto,
  ResponseTransactionSupplierDto
} from './transaction.dto';
import { ApolloQueryResult } from '@apollo/client';
import { Observable } from 'rxjs';
import {
  checkStatusTransactionSupplier,
  completeTransaction,
  createTransaction,
  detailTransactionSupplier,
  listTransactionSupplier
} from './transaction.constant';
import { ListApiTypes } from '../../../types/list-api.types';

@Injectable({
  providedIn: 'root'
})
export class SCMTransactionService {
  constructor(private apollo: Apollo) {}

  listTransactionSupplier(
    data: ListTransactionSupplierInputDto
  ): Observable<ApolloQueryResult<ResponseTransactionSupplierDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseTransactionSupplierDto>({
        query: listTransactionSupplier,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  detailTransactionSupplier(
    data: DetailTransactionSupplierInput
  ): Observable<ApolloQueryResult<ResponseDetailTransactionSupplierDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseDetailTransactionSupplierDto>({
        query: detailTransactionSupplier,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  completeTransaction(
    data: CompleteTransactionInput
  ): Observable<MutationResult<ResponseCompleteTransactionDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseCompleteTransactionDto>({
        mutation: completeTransaction,
        variables: data
      });
  }

  createTransaction(
    data: CreateTransactionInput
  ): Observable<MutationResult<ResponseCreateTransactionDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseCreateTransactionDto>({
        mutation: createTransaction,
        variables: data
      });
  }

  checkStatusTransactionSupplier(data: DetailTransactionSupplierInput
  ):Observable<MutationResult<ResponseCheckStatusTransactionSupplierDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseCheckStatusTransactionSupplierDto>({
        mutation: checkStatusTransactionSupplier,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }
}
