import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'lib-basic-checkbox',
  template: `
    <p-checkbox
      *ngIf="!disableChange"
      [(ngModel)]="checked"
      [binary]="true"
      [disabled]="disabled"
      [value]="value"
      (onChange)="onChange()">
    </p-checkbox>
    <div *ngIf="disableChange" class="overlay" (click)="onOverlayClick($event)">
      <p-checkbox
        [(ngModel)]="checked"
        [binary]="true"
        [disabled]="disabled"
        [value]="value"
        (onChange)="onChange()">
      </p-checkbox>
    </div>
  `,
  styleUrls: ['./basic-checkbox.component.scss']
})
export class BasicCheckboxComponent {
  @Input() checked = false;
  @Input() disabled = false;
  @Input() disableChange = false;
  @Input() value = '';
  @Output() handleChange: EventEmitter<void> = new EventEmitter();

  onChange() {
    this.handleChange.emit();
  }
  onOverlayClick(event: Event) {
    // You can still capture the click event here
    // this.handleChange.emit();
    event.stopPropagation(); // Stop the event from reaching the checkbox
  }
}
