import { Injectable } from '@angular/core';
import { ApolloQueryResult } from '@apollo/client';
import { Apollo } from 'apollo-angular';
import { Observable } from 'rxjs';
import { listRegency } from './regency.constant';
import { ListRegencyDto, ResponseListRegencyDto } from './regency.dto';
import { ListApiTypes } from '../../../types/list-api.types';

@Injectable({
  providedIn: 'root'
})
export class RegencyService {

  constructor(private apollo: Apollo) { }

  listRegency(data: ListRegencyDto): Observable<ApolloQueryResult<ResponseListRegencyDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseListRegencyDto>({
      query: listRegency,
      variables: data
    })
  }
}
