import { gql } from 'apollo-angular';

export const listHighlightProductVariant = gql`
  query listHighlightProductVariant($listHighlightProductVariantInput: ListHighlightProductVariantInput!) {
    listHighlightProductVariant(listHighlightProductVariantInput: $listHighlightProductVariantInput) {
      page
      limit
      total
      totalPage
      highlightProductVariants {
        id
        productVariant{
          id
          name
          priceBeforeDiscount
          price
          unitType
          unitQuantity
          totalUnitQuantity
          discount
          discountType
          isActive
          stock
          product{
            productVariants{
              name
            }
            name
            image
            description
            supplier{
              name
              image
            }
          }
          slug
        }
      }
    }
  }
`
