import { Injectable } from '@angular/core';
import { ApolloQueryResult } from '@apollo/client';
import { ListApiTypes } from '../../../types/list-api.types';
import { Apollo } from 'apollo-angular';
import { Observable } from 'rxjs';
import { listPostalCode } from './postal.constant';
import { ListPostalCodeDto, ResponseListPostalCodeDto } from './postal.dto';

@Injectable({
  providedIn: 'root'
})
export class PostalCodeService {
  constructor(private apollo: Apollo) {}

  listPostalCode(
    data: ListPostalCodeDto
  ): Observable<ApolloQueryResult<ResponseListPostalCodeDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseListPostalCodeDto>({
        query: listPostalCode,
        variables: data
      });
  }
}
