 import { Attribute, Directive, ElementRef, HostListener, Renderer2, ViewContainerRef } from "@angular/core";
import { LoadErrorComponent } from "./load-error.component";
import { LoadLoadingComponent } from "./load-loading.component";

@Directive({
  selector: "[lib-load-loading]"
})

export class LoadLoadingDirective {
  name!: string;

  constructor(
    @Attribute("errorStyle") public errorStyle: string,
    @Attribute("loadStyle") public loadStyle: string,
    private renderer: Renderer2,
    private el: ElementRef,
    private vcr: ViewContainerRef
  ) {
    const ref = this.vcr.createComponent(LoadLoadingComponent);
    Object.assign(ref.instance, {
      style: this.loadStyle
    });
    this.renderer.setStyle(this.el.nativeElement, "visibility", "hidden");
    this.renderer.setStyle(this.el.nativeElement, "position", "absolute");
    this.renderer.setStyle(this.el.nativeElement, "top", "0");
    // this.renderer.setStyle(this.el.nativeElement, "width", "10px");
    // this.renderer.setStyle(this.el.nativeElement, "height", "10px");
  }

  @HostListener("load") load() {
    this.vcr.clear();
    this.renderer.setStyle(this.el.nativeElement, "visibility", "visible");
    this.renderer.removeStyle(this.el.nativeElement, "position");
    this.renderer.removeStyle(this.el.nativeElement, "top");
    // this.renderer.removeStyle(this.el.nativeElement, "width");
    // this.renderer.removeStyle(this.el.nativeElement, "height");
  }

  @HostListener("error") onError() {
    this.vcr.clear();
    const ref = this.vcr.createComponent(LoadErrorComponent);
    Object.assign(ref.instance, {
      name: this.el.nativeElement.getAttribute("alt"),
      class: this.errorStyle
    });
    const img = this.el.nativeElement.parentElement.querySelector('img');
    this.el.nativeElement.remove(img)
    // img.nativeElement.remove();
  }
}
