import { Injectable } from '@angular/core';
import { Apollo } from 'apollo-angular';
import {
  DetailPlanOption,
  DetailSubscription,
  ListPlanDto,
  ListPlanOptionDto,
  ListPurchasePackageDto,
  ListSubscriptionTransactionDto,
  ResponseCheckEligibilityVoucherDto,
  ResponseDetailPlanOption,
  ResponseDetailSubscriptionTransactionDto,
  ResponseListPlanDto,
  ResponseListPlanOptionDto,
  ResponseListPurchasePackageDto,
  ResponseListSubscriptionTransactionDto
} from './subscription.dto';
import {CheckEligibleVoucherDto} from '../../supply-chain-public-api/voucher/voucher.dto';
import { Observable } from 'rxjs';
import { ApolloQueryResult } from '@apollo/client';
import { ListApiTypes } from '../../../types/list-api.types';
import {
  detailPlanOption,
  listPlan,
  listPlanOption,
  listPurchasePackage,
  listSubscriptionTransaction,
  detailSubscriptionTransaction,
  checkEligibleVoucher
} from './subscription.constant';

@Injectable({
  providedIn: 'root'
})
export class SubscriptionService {
  constructor(private apollo: Apollo) {}

  listPlan(
    data: ListPlanDto
  ): Observable<ApolloQueryResult<ResponseListPlanDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseListPlanDto>({
        fetchPolicy: 'no-cache',
        query: listPlan,
        variables: data
      });
  }

  listPlanOption(
    data: ListPlanOptionDto
  ): Observable<ApolloQueryResult<ResponseListPlanOptionDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseListPlanOptionDto>({
        fetchPolicy: 'no-cache',
        query: listPlanOption,
        variables: data
      });
  }

  detailPlanOption(
    data: DetailPlanOption
  ): Observable<ApolloQueryResult<ResponseDetailPlanOption>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseDetailPlanOption>({
        fetchPolicy: 'no-cache',
        query: detailPlanOption,
        variables: data
      });
  }

  listSubscriptionTransaction(
    data: ListSubscriptionTransactionDto
  ): Observable<ApolloQueryResult<ResponseListSubscriptionTransactionDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseListSubscriptionTransactionDto>({
        fetchPolicy: 'no-cache',
        query: listSubscriptionTransaction,
        variables: data
      });
  }
  detailSubscriptionTransaction(
    data: DetailSubscription
  ): Observable<ApolloQueryResult<ResponseDetailSubscriptionTransactionDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseDetailSubscriptionTransactionDto>({
        fetchPolicy: 'no-cache',
        query: detailSubscriptionTransaction,
        variables: data
      });
  }

  listPurchasePackage(
    data: ListPurchasePackageDto
  ): Observable<ApolloQueryResult<ResponseListPurchasePackageDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseListPurchasePackageDto>({
        fetchPolicy: 'no-cache',
        query: listPurchasePackage,
        variables: data
      });
  }

  checkEligibleVoucher(
    data: CheckEligibleVoucherDto
  ): Observable<ApolloQueryResult<ResponseCheckEligibilityVoucherDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseCheckEligibilityVoucherDto>({
        fetchPolicy: 'no-cache',
        query: checkEligibleVoucher,
        variables: data
      });
  }

}
