import { Injectable } from '@angular/core';
import { ApolloQueryResult } from '@apollo/client';
import { ListApiTypes } from '../../../types/list-api.types';
import { Apollo } from 'apollo-angular';
import { Observable } from 'rxjs';

import {
  ResponseDetailProductVariantDto,
  DetailProductVariantDto,
  ResponseListProductVariantDto,
  ListProductVariantDto,
  ListProductDto,
  ResponseListProductDto,
  DetailProductDto,
  ResponseDetailProductDto
} from './product.dto';
import {
  detailProduct,
  detailProductVariant,
  listProduct,
  listProductVariant
} from './product.constant';

@Injectable({
  providedIn: 'root'
})
export class ProductService {
  constructor(private apollo: Apollo) {}

  detailProductVariant(
    data: DetailProductVariantDto
  ): Observable<ApolloQueryResult<ResponseDetailProductVariantDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseDetailProductVariantDto>({
        query: detailProductVariant,
        variables: data
      });
  }

  listProductVariant(
    data: ListProductVariantDto
  ): Observable<ApolloQueryResult<ResponseListProductVariantDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListProductVariantDto>({
        fetchPolicy: 'no-cache',
        query: listProductVariant,
        variables: data
      });
  }

  listProduct(
    data: ListProductDto
  ): Observable<ApolloQueryResult<ResponseListProductDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListProductDto>({
        fetchPolicy: 'no-cache',
        query: listProduct,
        variables: data
      });
  }

  detailProduct(
    data: DetailProductDto
  ): Observable<ApolloQueryResult<ResponseDetailProductDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseDetailProductDto>({
        fetchPolicy: 'no-cache',
        query: detailProduct,
        variables: data
      });
  }
}
