import { gql } from 'apollo-angular';

export const currentTier = gql`
  query currentTier {
    currentTier {
      currentPosition
      status
      tier {
        id
        name
        displayName
        icon
      }
    }
  }
`;
export const userTierDetail = gql`
  query userTierDetail {
    userTierDetail {
      currentPosition
      status
      tier {
        id
        name
        displayName
        icon
        backgroundColor
        endProgram
        position {
          sequence
        }
        tierPrograms {
          id
          type
          operator
          criterias {
            totalPrice
            totalTransaction
            name
            type
            criteriaProgression {
              status
              totalPrice
              totalTransaction
              type
              plan {
                id
                displayName
                name
              }
            }
          }
        }
      }
      nextTier {
        id
        name
        displayName
        icon
        backgroundColor
        endProgram
        position {
          sequence
        }
        tierPrograms {
          id
          type
          operator
          criterias {
            totalPrice
            totalTransaction
            name
            type
            criteriaProgression {
              status
              totalPrice
              totalTransaction
              type
              plan {
                id
                displayName
                name
              }
            }
          }
        }
      }
    }
  }
`;
export const listTierActive = gql`
  query listTierActive {
    listTierActive {
      tiers {
        name
        backgroundColor
        displayName
        endProgram
        id
        position {
          sequence
        }
        rewards {
          tierId
          id
          name
          icon
          description
        }
      }
    }
  }
`;
