import { Injectable } from '@angular/core';
import { ApolloQueryResult } from '@apollo/client';
import { Apollo } from 'apollo-angular';
import { Observable } from 'rxjs';
import { listDistrict } from './district.constant';
import { ListDistrictDto, ResponseListDistrictDto } from './district.dto';
import { ListApiTypes } from '../../../types/list-api.types';

@Injectable({
  providedIn: 'root'
})
export class DistrictService {

  constructor(private apollo: Apollo) { }

  listDistrict(data: ListDistrictDto): Observable<ApolloQueryResult<ResponseListDistrictDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseListDistrictDto>({
      query: listDistrict,
      variables: data
    })
  }
}
