import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'discountTypePipe'
})
export class DiscountTypePipe implements PipeTransform {
  transform(value: number, type: string): string {
    if (type == 'PERCENTAGE') {
      return 'Hemat ' + value + '%';
    } else if (type == 'FIXED') {
      const data = value.toLocaleString('ID');
      return 'Hemat Rp' + data;
    } else if (type == 'SHORTCURRENCY') {
      let val: string | number = Math.abs(value);
      if (val >= 10 ** 3 && val < 10 ** 6) {
        val = (val / 1000).toFixed(0) + ' rb';
        return 'Hemat Rp ' + val;
      } else if (val >= 10 ** 6) {
        val = (val / 1000000).toFixed(0) + ' jt';
        return 'Hemat Rp ' + val;
      }
      return val.toString();
    } else {
      return value.toString();
    }
  }
}
