import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'textLimitPipe'
})
export class TextLimitPipe implements PipeTransform {

  transform(value= '', limit =0, isLimit = false): string {
    if(limit !== 0 && isLimit){
      return value.substr(0, limit);
    }else {
      return value
    }
  }

}
