import { Injectable } from '@angular/core';
import { ApolloQueryResult } from '@apollo/client';
import { ListApiTypes } from '../../../types/list-api.types';
import { Apollo } from 'apollo-angular';
import { Observable } from 'rxjs';
import { listProvince } from './province.constant';
import { ListProvinceDto, ResponseListProvinceDto } from './province.dto';

@Injectable({
  providedIn: 'root'
})
export class ProvinceService {

  constructor(private apollo: Apollo) { }

  listProvince(data: ListProvinceDto): Observable<ApolloQueryResult<ResponseListProvinceDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseListProvinceDto>({
      query: listProvince,
      variables: data
    })
  }
}
