import {
  Component,
  ComponentFactory,
  ComponentFactoryResolver,
  ComponentRef, OnDestroy, OnInit,
  Type,
  ViewChild,
  ViewContainerRef
} from '@angular/core';
import { Modal } from '../../../service/modal/modal.model';
import { InputModalDto } from '../../../service/modal/modal.service';
import { CommonModule } from '@angular/common';
import { animate, style, transition, trigger } from '@angular/animations';

@Component({
  standalone: true,
  selector: 'bottom-modal',
  animations: [
    trigger('enterAnimation', [
      transition(':enter', [
        style({
          transform: 'translateY(100%)',
          backgroundColor: 'transparent'
        }),
        animate('300ms', style({ transform: 'translateY(0%)' })),
        animate('300ms', style({ backgroundColor: '#0000007F' }))
      ]),
      transition(':leave', [
        style({ height: '100%', opacity: 1 }),
        animate('1000ms', style({ height: '0', opacity: 0 }))
      ])
    ])
  ],
  template: ` <div
    [@enterAnimation]="true"
    [ngClass]="{ transition: isShow }"
    class="flex fixed h-full inset-0 z-[99] justify-center items-end bg-[#0000007F] animate-modal-bottom">
    <div
      class="flex flex-col py-4 bg-white w-full max-w-[500px] rounded-t-3xl h-8 p-4"
      style="height:{{ height ? height : 'auto' }}">
      <div class="header">
        <ng-template #headerContainer></ng-template>
      </div>
      <div class="modal-body">
        <ng-template #modalContainer></ng-template>
      </div>
    </div>
  </div>`,
  imports: [CommonModule]
})
export class BottomModalComponent implements OnInit, OnDestroy{
  @ViewChild('modalContainer', { read: ViewContainerRef })
  private modalContainer!: ViewContainerRef;
  @ViewChild('headerContainer', { read: ViewContainerRef })
  private headerContainer!: ViewContainerRef;
  height: string | undefined = 'auto';
  isShow = false;

  constructor(private componentFactoryResolver: ComponentFactoryResolver) {}

  createModal<T extends Modal>(
    component: Type<T>,
    input?: InputModalDto
  ): ComponentRef<T> {
    this.modalContainer.clear();
    this.height = input?.height;
    const factory: ComponentFactory<T> =
      this.componentFactoryResolver.resolveComponentFactory(component);
    return this.modalContainer.createComponent(factory, 0);
  }

  createHeader<T extends Modal>(component: Type<T>): ComponentRef<T> {
    this.headerContainer.clear();
    const factory: ComponentFactory<T> =
      this.componentFactoryResolver.resolveComponentFactory(component);
    return this.headerContainer.createComponent(factory, 0);
  }

  ngOnInit() {
    this.isShow = true;
  }

  ngOnDestroy() {
    this.isShow = false;
  }
}
