import { gql } from 'apollo-angular';

export const listTransactionSupplier = gql`
  query listTransactionSupplier(
    $listTransactionSupplierInput: ListTransactionSupplierInput!
  ) {
    listTransactionSupplier(
      listTransactionSupplierInput: $listTransactionSupplierInput
    ) {
      page
      limit
      totalPage
      transactionSuppliers {
        id
        supplierId
        supplierName
        supplierImage
        totalPrice
        status
        totalQuantity
        deliveryCourierId
        invoice
        totalPrice
        updatedAt
        tierImage
        supplierThirdPartyType
        supplier {
          id
          name
          slug
        }
        warehouse {
          slug
        }
        transactionDetails {
          productName
          productVariantName
          productVariantQuantity
          productVariantImage
        }
        transaction {
          id
          type
        }
      }
    }
  }
`;

export const detailTransactionSupplier = gql`
  query detailTransactionSupplier($id: ID!) {
    detailTransactionSupplier(id: $id) {
      updatedAt
      id
      supplierId
      supplierName
      warehouseName
      supplierEmail
      postalCode
      addressDetail
      recipientName
      recipientPhone
      recipientEmail
      totalTransactionDetail
      invoice
      status
      totalQuantity
      totalPrice
      note
      supplierImage
      tierImage
      thirdPartyTransactionRefId
      supplierThirdPartyType
      warehouse {
      id
      name
      slug
    }
      transactionDetails {
        id
        productName
        productVariantName
        productVariantImage
        productVariantQuantity
        productVariantUnitType
        productVariantUnitQuantity
        subtotal
        productVariantPrice
        discount
        discountType
        discountAmount
        productVariant {
          slug
          unitQuantity
          stock
        }
        productVariantWarehouse {
          id
        }
      }
      totalPriceBeforeDeliveryFee
      totalPriceBeforeAdjustment
      transactionSupplierPriceAdjustments {
        id
        priceAdjustmentName
        priceAdjustmentAmount
        priceAdjustmentBaseCalculation
        priceAdjustmentId
      }
      payment {
        id
        issuerName
        paymentType
        transactionRefId
        paymentCode
        transactionAmount
        transactionDate
        paymentStatus
        expiredAt
      }
      transaction {
        id
        type
        invoice
        total
      }
      supplier{
        id
        name
        slug
      }
    }
  }
`;

export const completeTransaction = gql`
  mutation completeTransaction($id: ID!) {
    completeTransaction(id: $id) {
      id
    }
  }
`;

export const createTransaction = gql`
  mutation createTransaction($createTransactionInput: CreateTransactionInput!) {
    createTransaction(createTransactionInput: $createTransactionInput) {
      id
      status
      invoice
      total
      userId
      payment {
        id
        paymentCode
        transactionRefId
        transactionAmount
        paymentType
        expiredAt
        paymentFee
        issuerName
      }
      createdAt
      updatedAt
    }
  }
`;

export const checkStatusTransactionSupplier = gql`
mutation checkStatusTransactionSupplier($id: ID!){
  checkStatusTransactionSupplier(id: $id){
    id
    status
  }
}
`
