import { Injectable } from "@angular/core";
import { AuthData } from "../data/auth.data";
import { WindowActionService } from "./window-action.service";
import {Address, getDetailLocationDto, LocationApiService} from "@youtap-web-lib";

export interface positionDto {
  lng: number;
  lat: number;
}

@Injectable({
  providedIn: "root"
})
export class LocationService {
  constructor(
    private locationApiService: LocationApiService,
    private authData: AuthData,
    private windowActionService: WindowActionService
  ) {
  }

  public setLocation() {
    this.getPosition().then((pos) => {
      const location: getDetailLocationDto = {
        detailLocationInput: {
          latitude: pos.lat,
          longitude: pos.lng
        }
      };
      this.detailLocation(location)
    });
  }


  public detailLocation(location: getDetailLocationDto){
    this.locationApiService
      .detailLocation(location)
      .toPromise()
      .then(({ data }: any) => {
        const address: Address = {
          id: '',
          name: '',
          postalCode: '',
          addressDetail: '',
          recipientName: '',
          recipientPhone: '',
          recipientEmail: '',
          isDefault: false,
          latitude: data.detailLocation.latitude,
          longitude: data.detailLocation.longitude,
          province: {
            id: data.detailLocation.village.district?.regency?.province.id,
            name: data.detailLocation.village.district?.regency?.province.name

          },
          regency: {
            id: data.detailLocation.village.district.regency.id,
            name: data.detailLocation.village.district.regency.name

          },
          district: {
            id: data.detailLocation.village.district.id,
            name: data.detailLocation.village.district.name
          },
          village: {
            id: data.detailLocation.village.id,
            name: data.detailLocation.village.name
          }

        };
        this.authData.location = address;
        this.windowActionService.reload()
      })
  }

  getPosition(): Promise<positionDto> {
    return new Promise((resolve, reject) => {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (resp) => {
            resolve({ lng: resp.coords.longitude, lat: resp.coords.latitude });
          },
          (err) => {
            reject(err);
          }
        );
      }
    });
  }
}
