import { Directive, Input, ViewContainerRef, TemplateRef, OnChanges } from "@angular/core";
import { SkeletonLoadingComponent } from "./skeleton-loading.component";
// import { SkeletonLoadingComponent } from "../../../component/src/lib/loadings/skeleton-loading/skeleton-loading.component";

interface loading {
  isLoading: boolean;
  width?: string;
  height?: string;
}

@Directive({ selector: '[lib-skeleton]' })
export class SkeletonLoadingDirective implements OnChanges{
  @Input('lib-skeleton') loading!: loading;
  constructor(
    // eslint-disable-next-line  @typescript-eslint/no-explicit-any
    private tpl: TemplateRef<any>,
    private vcr: ViewContainerRef
  ) {}

  ngOnChanges() {
    if (this.loading.isLoading) {
      this.vcr.clear();
      const ref = this.vcr.createComponent(SkeletonLoadingComponent);
      Object.assign(ref.instance, {
        width: this.loading.width,
        height: this.loading.height,
      })
    }else {
      this.vcr.clear()
      this.vcr.createEmbeddedView(this.tpl);
    }
  }
}
