import { Component, Input } from '@angular/core';
import {
  TransactionSupplierPriceAdjustments
} from "../../../graphql/supply-chain-public-api/transaction/transaction.dto";

export interface TransactionPaymentMethod {
  issuerName: string;
  paymentType: string;
  qty: number;
  totalPrice: number;
  subtotal: number;
  adjustment: number;
  deliveryFee: number;
  totalPayment: number;
  transactionSupplierPriceAdjustments?: TransactionSupplierPriceAdjustments[];
}

@Component({
  selector: 'lib-payment-detail-section',
  templateUrl: './payment-detail-section.component.html'
})
export class PaymentDetailSectionComponent {
  @Input() transaction!: TransactionPaymentMethod;
  @Input() paymentMethod!: string;

  replacePaymentName(type: string, name: string) {
    if(type === 'QRIS') {
      return 'QRIS';
    }
    else {
      switch (name) {
        case 'bca_va':
          return 'BCA Virtual Account';
        case 'mandiri_va':
          return 'Mandiri Virtual Account';
        case 'bri_va':
          return 'BRI Virtual Account';
        case 'bni_va':
          return 'BNI Virtual Account';
        case 'mpm':
          return 'QRIS';

        default:
          return name;
      }
    }
  }
}
