import { Injectable } from '@angular/core';
import { Apollo } from 'apollo-angular';
import { Observable } from 'rxjs';
import { ApolloQueryResult } from '@apollo/client';
import {
  ListHighlightDto,
  ResponseListHighlightDto,
  ResponseListHighlightProductVariantWarehouseDto,
  ResponseListSectionDto,
  ListHighlightProductVariantWarehouseDto
} from './section.dto';
import { ListApiTypes } from '../../../types/list-api.types';
import {listHighlight, listHighlightProductVariantWarehouse, listSection} from './section.constant';

@Injectable({
  providedIn: 'root'
})
export class SectionService {
  constructor(private apollo: Apollo) {}

  listSection(): Observable<ApolloQueryResult<ResponseListSectionDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListSectionDto>({
        fetchPolicy: 'no-cache',
        query: listSection
      });
  }

  listHighlight(data: ListHighlightDto): Observable<ApolloQueryResult<ResponseListHighlightDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListHighlightDto>({
        fetchPolicy: 'no-cache',
        query: listHighlight,
        variables: data
      });
  }

  listHighlightProductVariantWarehouse(data: ListHighlightProductVariantWarehouseDto): Observable<ApolloQueryResult<ResponseListHighlightProductVariantWarehouseDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListHighlightProductVariantWarehouseDto>({
        fetchPolicy: 'no-cache',
        query: listHighlightProductVariantWarehouse,
        variables: data
      });
  }
}
