import { Injectable } from '@angular/core';
import { Apollo, MutationResult } from 'apollo-angular';
import { Observable } from 'rxjs';
import { ApolloQueryResult } from '@apollo/client';
import {
  ListUserInboxInputDto, ReadUnreadAllUserInboxInputDto,
  ReadUnreadUserInboxInputDto,
  ResponseListUserInboxDto, ResponseReadUnreadAllUserInboxDto,
  ResponseReadUnreadUserInboxDto
} from "./inbox.dto";
import {ListApiTypes} from "../../../types/list-api.types";
import {listUserInbox, readUnreadAllInbox, readUnreadInbox} from "./inbox.constant";

@Injectable({
  providedIn: 'root'
})
export class InboxService {
  constructor(private apollo: Apollo) {}

  listUserInbox(
    listUserInboxInput: ListUserInboxInputDto
  ): Observable<ApolloQueryResult<ResponseListUserInboxDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseListUserInboxDto>({
        query: listUserInbox,
        variables: listUserInboxInput,
        fetchPolicy: 'no-cache'
      });
  }

  readUnreadUserInbox(
    readUnreadUserInboxInputDto: ReadUnreadUserInboxInputDto
  ): Observable<MutationResult<ResponseReadUnreadUserInboxDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseReadUnreadUserInboxDto>({
        mutation: readUnreadInbox,
        variables: readUnreadUserInboxInputDto
      });
  }
  readUnreadAllInbox(
    readUnreadAllUserInboxInputDto: ReadUnreadAllUserInboxInputDto
  ): Observable<MutationResult<ResponseReadUnreadAllUserInboxDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseReadUnreadAllUserInboxDto>({
        mutation: readUnreadAllInbox,
        variables: readUnreadAllUserInboxInputDto
      });
  }
}
