import {
  Component,
  ContentChild,
  EventEmitter,
  Input,
  Output
} from '@angular/core';

@Component({
  selector: 'lib-bottom-modal',
  templateUrl: './bottom-modal.component.html'
})
/* tslint:disable */
export class BottomModalComponent {
  @Input() showModal = false;
  @Input() padding = '1rem';
  @Input() backdropDismiss = true;
  // eslint-disable-next-line  @typescript-eslint/no-explicit-any
  @ContentChild('headerTemplate') headerTemplate: any;
  // eslint-disable-next-line  @typescript-eslint/no-explicit-any
  @ContentChild('bodyTemplate') bodyTemplate: any;
  // eslint-disable-next-line  @typescript-eslint/no-explicit-any
  @ContentChild('footerTemplate') footerTemplate: any;
  @Output() closeOutsideModal: EventEmitter<string> = new EventEmitter();

  handleCloseOutsideModal(event: MouseEvent): void {
    if(this.backdropDismiss) {
      const overlayElement = document.querySelector('.modal');
      if (overlayElement === event.target) {
        this.showModal = false;
        this.closeOutsideModal.emit();
        // Stop event propagation to prevent further clicks on elements beneath the modal
        event.stopPropagation();
      }
    }
  }
}
