/* eslint-disable @typescript-eslint/no-explicit-any */
import {Injectable, OnDestroy} from '@angular/core';
import {environment} from 'apps/youtap-bos/src/environments/environment';
import {WindowActionService} from './window-action.service';
import {ReferralService} from "./referral.service";

@Injectable({
  providedIn: 'root'
})
export class DeeplinkService implements OnDestroy {
  timeOut: any
  onProcess = false

  constructor(
    private windowActionService: WindowActionService,
    private referralService: ReferralService) {
  }

  deeplink() {
    const ua = navigator.userAgent.toLowerCase();
    const isAndroid = ua.indexOf('android') > -1; // android check
    const isIphone = ua.indexOf('iphone') > -1; // ios check
    let appUrlSchema = environment.BOS_APP_URL_SCHEMA + this.windowActionService.getPathNameAndSearch();
    const referral = this.referralService.getAllReferral().length ? JSON.stringify(this.referralService.getAllReferral()) : ''
    if (this.windowActionService.checkQueryParams() && referral) {
      appUrlSchema = appUrlSchema + '&referral=' + referral
    }else {
      appUrlSchema = appUrlSchema + '?referral=' + referral
    }
    this.onProcess = true
    if (isIphone == true) {
      this.timeOut = setTimeout(() => {
        if (this.onProcess) {
          window.location.href = environment.BOS_APP_STORE_URL
          this.onProcess = false
        }
      }, 3000);
      window.location.href = appUrlSchema;

      window.onblur = () => {
        clearTimeout(this.timeOut)
      };

      window.onfocus = () => {
        this.timeOut = setTimeout(() => {
          if (this.onProcess) {
            window.location.href = environment.BOS_APP_STORE_URL
            this.onProcess = false
          }
        }, 3000);
      }

    } else if (isAndroid == true) {
      this.timeOut = setTimeout(function () {
        window.location.href = environment.BOS_GOOGLE_PLAY_URL
      }, 2000);
      window.location.href = appUrlSchema;

      window.onblur = () => {
        clearTimeout(this.timeOut)
        this.onProcess = false
      };

      window.onfocus = () => {
        this.timeOut = setTimeout(() => {
          if (this.onProcess) {
            window.location.href = environment.BOS_APP_STORE_URL
            this.onProcess = false
          }
        }, 3000);
      }
    }
  }

  ngOnDestroy() {
    clearTimeout(this.timeOut)
  }

  toAppPos() {
    this.windowActionService.open(environment.POS_APP_URL, '_blank')
  }

}
