import { Injectable } from '@angular/core';
import { Apollo, MutationResult } from 'apollo-angular';
import {
  CreateAddressDetailInputDto,
  CreateBankAccountInputDto,
  CreatePersonalInformationInputDto,
  ResponseCreateAddressDetailDto,
  ResponseCreateBankAccountDto,
  ResponseCreateMerchantKycDto,
  ResponseCreatePersonalInformationDto,
  ResponseDetailDto,
  DetailBankInputDto,
  DetailProvinceInputDto,
  DetailRegencyInputDto,
  DetailDistrictInputDto,
  DetailPostalCodeInputDto,
  DetailVillageInputDto,
  ReadMediaRegistrationGCSInputDto,
  ResponseReadMediRegistrationGCSDto, ResponseDetailMerchantRegistrationUserDto
} from "./verification.dto";
import { Observable } from 'rxjs';
import {
  createAddressMutation,
  createBankAccountMutation,
  createMerchantKycMutation,
  createPersonalInfoMutation,
  detailBank,
  detailDistrict, detailMerchantRegistrationUser,
  detailPostalCode,
  detailProvince,
  detailRegency,
  detailVillageName,
  readMediaRegistrationGCS
} from "./verification.constant";
import { ListApiTypes } from '../../../types/list-api.types';
import { ApolloQueryResult } from '@apollo/client';

@Injectable({
  providedIn: 'root'
})
export class VerificationService {
  constructor(private apollo: Apollo) {}

  detailMerchantRegistrationUser(): Observable<ApolloQueryResult<ResponseDetailMerchantRegistrationUserDto>>{
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseDetailMerchantRegistrationUserDto>({
        query: detailMerchantRegistrationUser,
        fetchPolicy: 'no-cache',
      });
  }

  createAddressMutation(
    data: CreateAddressDetailInputDto
  ): Observable<MutationResult<ResponseCreateAddressDetailDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseCreateAddressDetailDto>({
        mutation: createAddressMutation,
        variables: data
      });
  }

  createPersonalInfoMutation(
    data: CreatePersonalInformationInputDto
  ): Observable<MutationResult<ResponseCreatePersonalInformationDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseCreatePersonalInformationDto>({
        mutation: createPersonalInfoMutation,
        variables: data
      });
  }
  createBankAccountMutation(
    data: CreateBankAccountInputDto
  ): Observable<MutationResult<ResponseCreateBankAccountDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseCreateBankAccountDto>({
        mutation: createBankAccountMutation,
        variables: data
      });
  }
  createMerchantKycMutation(): Observable<
    MutationResult<ResponseCreateMerchantKycDto>
  > {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseCreateMerchantKycDto>({
        mutation: createMerchantKycMutation
      });
  }

  detailProvince(
    data: DetailProvinceInputDto
  ): Observable<ApolloQueryResult<ResponseDetailDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseDetailDto>({
        query: detailProvince,
        variables: data
      });
  }
  detailRegency(
    data: DetailRegencyInputDto
  ): Observable<ApolloQueryResult<ResponseDetailDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseDetailDto>({
        query: detailRegency,
        variables: data
      });
  }
  detailDistrict(
    data: DetailDistrictInputDto
  ): Observable<ApolloQueryResult<ResponseDetailDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseDetailDto>({
        query: detailDistrict,
        variables: data
      });
  }
  detailVillage(
    data: DetailVillageInputDto
  ): Observable<ApolloQueryResult<ResponseDetailDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseDetailDto>({
        query: detailVillageName,
        variables: data
      });
  }
  detailPostalCode(
    data: DetailPostalCodeInputDto
  ): Observable<ApolloQueryResult<ResponseDetailDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseDetailDto>({
        query: detailPostalCode,
        variables: data
      });
  }
  detailBank(
    data: DetailBankInputDto
  ): Observable<ApolloQueryResult<ResponseDetailDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseDetailDto>({
        query: detailBank,
        variables: data
      });
  }
  readMediaRegistrationGCS(
    data: ReadMediaRegistrationGCSInputDto
  ): Observable<ApolloQueryResult<ResponseReadMediRegistrationGCSDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseReadMediRegistrationGCSDto>({
        query: readMediaRegistrationGCS,
        variables: data,
        fetchPolicy: 'no-cache',
      });
  }
}
