import { gql } from 'apollo-angular';

export const listPlan = gql`
  query listPlan($listPlanInput: ListPlanInput!, $referralCode: String) {
  listPlan(listPlanInput: $listPlanInput) {
    limit
    page
    total
    totalPage
    plans {
      id
      name
      isActive
      displayName
      description
      basePrice
      planOptions {
        id
        price
        term
        termType
        type
        image
        isAffiliate(referralCode: $referralCode)
        plan {
          name
          displayName
        }
      }
      planFeatures {
        feature {
          id
          displayName
          image
          description
          isActive
        }
      }
    }
  }
}
`;

export const listPlanOption = gql`
  query listPlanOption($listPlanOptionInput: ListPlanOptionInput!) {
    listPlanOption(listPlanOptionInput: $listPlanOptionInput) {
      planOptions {
        id
        price
        term
        termType
        type
        image
        plan {
          id
          basePrice
          description
          displayName
          image
          isActive
          name
        }
      }
    }
  }
`;

export const detailPlanOption = gql`
  query getPlanOption($id: ID!) {
    detailPlanOption(id: $id) {
      id
      price
      term
      termType
      type
      plan {
        id
        basePrice
        description
        displayName
        image
        isActive
        name
        planFeatures {
          feature {
            id
            displayName
            image
            description
            isActive
          }
        }
      }
    }
  }
`;

export const listSubscriptionTransaction = gql`
  query listSubscriptionTransaction(
    $listSubscriptionTransactionInput: ListSubscriptionTransactionInput!
  ) {
    listSubscriptionTransaction(
      listSubscriptionTransactionInput: $listSubscriptionTransactionInput
    ) {
      page
      limit
      total
      totalPage
      subscriptionTransaction {
        id
        createdAt
        priceBeforeVoucher
        status
        total
        invoice
        subscriptionTransactionDetails {
          id
          planDisplayName
          planOptionTerm
          planOptionTermType
          planOption {
            id
          }
        }
      }
    }
  }
`;
export const detailSubscriptionTransaction = gql`
  query detailSubscriptionTransaction($id: ID!) {
    detailSubscriptionTransaction(id: $id) {
      id
      invoice
      status
      total
      priceBeforeVoucher
      updatedAt
      subscriptionTransactionDetails {
        createdAt
        planDisplayName
        planBasePrice
        planOptionPrice
        planOption {
          id
          term
          termType
        }
      }
    }
  }
`;

export const listPurchasePackage = gql`
  query listPurchasePackage(
    $listPurchasePackageInputPaid: ListPurchasePackageInput!
    $listPurchasePackageInputPaidExpired: ListPurchasePackageInput!
  ) {
    PAID: listPurchasePackage(
      listPurchasePackageInput: $listPurchasePackageInputPaid
    ) {
      subscriptionPurchases {
        id
        startDate
        endDate
        isExpired
        expiresIn
        plan {
          id
          image
          name
          displayName
          basePrice
          description
          isActive
          planOptions {
            id
            price
            term
            termType
          }
          planFeatures {
            id
            feature {
              id
              image
              name
              displayName
              description
              isActive
            }
          }
        }
        planOption {
          id
          price
          term
          termType
        }
        isExpired
      }
    }
    PAID_EXPIRED: listPurchasePackage(
      listPurchasePackageInput: $listPurchasePackageInputPaidExpired
    ) {
      subscriptionPurchases {
        id
        startDate
        endDate
        isExpired
        expiresIn
        plan {
          id
          image
          name
          displayName
          basePrice
          description
          isActive
          planOptions {
            id
            price
            term
            termType
          }
          planFeatures {
            id
            feature {
              id
              image
              name
              displayName
              description
              isActive
            }
          }
        }
        planOption {
          id
          price
          term
          termType
        }
        isExpired
      }
    }
  }
`;

export const checkEligibleVoucher = gql`
  query checkEligibleVoucher(
    $checkEligibleVoucherInput: CheckEligibleVoucherInput!
  ) {
    checkEligibleVoucher(
      checkEligibleVoucherInput: $checkEligibleVoucherInput
    ) {
      id
      couponCode
      status
      voucherCode
      voucherType
      voucherValue
    }
  }
`;
