import { Injectable } from "@angular/core";
import { Apollo } from "apollo-angular";
import { Observable } from "rxjs";
import { ListPartnerAllDto, ResponseListPartnerAllDto } from "./partner.dto";
import { ApolloQueryResult } from "@apollo/client";
import { ListApiTypes } from "../../../types/list-api.types";
import { listPartnerAll } from "./partner.constant";

@Injectable({
  providedIn: "root"
})
export class PartnerService {
  constructor(private apollo: Apollo) {
  }


  listOfficialPartner(data: ListPartnerAllDto):
    Observable<ApolloQueryResult<ResponseListPartnerAllDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseListPartnerAllDto>({
      query: listPartnerAll,
      variables: data
    });
  }
}
