import {Component, HostListener, OnInit} from '@angular/core';
import {environment} from '../../../../../environments/environment';
import {WindowActionService} from '../../../service/window-action.service';
import {GaService} from '../../../service/ga.service';
import {ReferralService} from "../../../service/referral.service";

@Component({
  selector: 'app-notification-section',
  templateUrl: './app-notification-section.component.html'
})
export class AppNotificationSectionComponent implements OnInit {
  isShow = true;
  isModalDownload = false;

  constructor(
    private windowActionService: WindowActionService,
    private gaService: GaService,
    private referralService: ReferralService
  ) {
  }

  ngOnInit(): void {
    const isShowNotifApp = localStorage.getItem('isShowNotifApp');
    const pathName = this.windowActionService.getPathName();
    if (isShowNotifApp && isShowNotifApp === 'false') {
      this.isShow = false;
    }
    if (pathName === '/campaign') {
      this.isShow = false;
    }
  }

  @HostListener('document:visibilitychange', ['$event'])
  public visibilitychange(): void {
    if (document.hidden) {
      this.closeModal();
    }
  }

  closeNotif() {
    this.gaService.addEvent('transition_notification_click_button_close', 'transition', '');
    this.isShow = false;
    localStorage.setItem('isShowNotifApp', 'false');
  }

  openApp() {
    this.gaService.addEvent('transition_notification_click_buka_app', 'transition', '');
    let app =
      environment.BOS_APP_URL_SCHEMA +
      this.windowActionService.getPathNameAndSearch();
    const referral = this.referralService.getAllReferral().length ? JSON.stringify(this.referralService.getAllReferral()) : ''
    if (this.windowActionService.checkQueryParams() && referral) {
      app = app + '&referral=' + referral
    }else {
      app = app + '?referral=' + referral
    }
    window.location.href = app;
    setTimeout(() => {
      this.isModalDownload = true;
    }, 700);
  }

  toGooglePlay() {
    this.gaService.addEvent('transition_click_button_playstore', 'transition', '');
    window.open(environment.BOS_GOOGLE_PLAY_URL);
  }

  toAppStore() {
    this.gaService.addEvent('transition_click_button_appstore', 'transition', '');
    window.open(environment.BOS_APP_STORE_URL);
  }

  closeModal() {
    this.isModalDownload = false;
  }
}
