import { Pipe, PipeTransform } from "@angular/core";
export enum TermTypesNew {
  DAY = "DAY",
  MONTH = "MONTH",
  WEEK = "WEEK",
  YEAR = "YEAR",
  DAILY = "DAILY",
  WEEKLY = "WEEKLY",
  MONTHLY = "MONTHLY"
}
@Pipe({
  name: "TimeUnitPipe"
})
export class TimeUnitPipe implements PipeTransform {

  transform(termType: string): string {
    switch (termType) {
      case TermTypesNew.DAY:
        return 'hari';
      case TermTypesNew.DAILY:
        return 'hari';
      case TermTypesNew.WEEK:
        return 'minggu';
      case TermTypesNew.WEEKLY:
        return 'minggu';
      case TermTypesNew.MONTH:
        return 'bulan';
      case TermTypesNew.MONTHLY:
        return 'bulan';
      case TermTypesNew.YEAR:
        return 'tahun';
      default:
        return '';
    }
  }
}
