import { Injectable } from '@angular/core';
import { Apollo, MutationResult } from 'apollo-angular';
import {
  CreateCheckPhoneInputDto, CreateMerchantInputDto,
  CreatePINInputDto,
  LoginKYCInputDto,
  ResponseCheckPhonetDto, ResponseCreateMerchantDto,
  ResponseCreatePINDto, ResponseGetStatusKycDto,
  ResponseLoginKYCDto, ResponseRegenerateOtpDto,
  ResponseValidateOTPtDto,
  ValidateOTPInputDto
} from './register.dto';
import { Observable } from 'rxjs';
import {
  checkPhoneMutation, createMerchant,
  createPINMutation, getStatusKyc,
  loginKYCMutation, regenerateOTP,
  validateOTPMutation
} from './register.constant';
import { ListApiTypes } from '../../../types/list-api.types';
import {ApolloQueryResult} from "@apollo/client";

@Injectable({
  providedIn: 'root'
})
export class RegisterService {
  constructor(private apollo: Apollo) {}

  createCheckPhoneMutation(
    data: CreateCheckPhoneInputDto
  ): Observable<MutationResult<ResponseCheckPhonetDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseCheckPhonetDto>({
        mutation: checkPhoneMutation,
        variables: data
      });
  }

  validateOTPMutation(
    data: ValidateOTPInputDto
  ): Observable<MutationResult<ResponseValidateOTPtDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseValidateOTPtDto>({
        mutation: validateOTPMutation,
        variables: data
      });
  }

  createPINMutation(
    data: CreatePINInputDto
  ): Observable<MutationResult<ResponseCreatePINDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseCreatePINDto>({
        mutation: createPINMutation,
        variables: data
      });
  }

  loginKYCMutation(
    data: LoginKYCInputDto
  ): Observable<MutationResult<ResponseLoginKYCDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseLoginKYCDto>({
        mutation: loginKYCMutation,
        variables: data
      });
  }

  regenerateOTP(): Observable<MutationResult<ResponseRegenerateOtpDto>> {
    return this.apollo.use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseRegenerateOtpDto>({
        mutation: regenerateOTP,
      })
  }

  createMerchant(data: CreateMerchantInputDto): Observable<MutationResult<ResponseCreateMerchantDto>> {
    return this.apollo.use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseCreateMerchantDto>({
        mutation: createMerchant,
        variables: data,
        fetchPolicy: 'no-cache',
      })
  }

  getStatusKyc(): Observable<ApolloQueryResult<ResponseGetStatusKycDto>> {
    return this.apollo.use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseGetStatusKycDto>({
        query: getStatusKyc,
        fetchPolicy: 'no-cache',
      });
  }

}
