import {Component, EventEmitter, Input, Output} from '@angular/core';

@Component({
  selector: 'lib-supplier-ajak-cuan-card',
  templateUrl: './supplier-ajak-cuan-card.component.html'
})
export class SupplierAjakCuanCardComponent {
  @Input() image = '';
  @Input() imageTier = '';
  @Input() name = '';
  @Input() priceType = 'ITEM_PRICE_TYPE_FIXED';
  @Input() commission = 0;
  @Input() time: string | Date = 'Tanpa Batasan';
  @Output() showCopyModal = new EventEmitter<boolean>;

  onCopyModal() {
    this.showCopyModal.emit();
  }
}
