import { Component, Input } from "@angular/core";
import { CommonModule } from "@angular/common";

@Component({
  standalone: true,
  selector: "lib-load-error",
  imports: [
    CommonModule
  ],
  template: `
    <div class="{{class}} flex items-center justify-center">
      <p class="text-primary-yt-blue">{{ initials(name) }}</p>
    </div>
  `
})
export class LoadErrorComponent {
  @Input() name = "YT";
  @Input() class = "";

  initials(value: string): string {
    const fullName = value.split(" ");
    const first = fullName.shift();
    const last = fullName.pop();
    const lastInit = last ? last?.charAt(0) : "T";
    const firstInit = first ? first.charAt(0) : "Y";
    return firstInit + lastInit;
    // const data = value ? value : 'YT';
    // return data.match(/(\b\S)?/g).join("").match(/(^\S|\S$)?/g).join("").toUpperCase();
  }
}
