import { Component, EventEmitter, Output } from '@angular/core';
import { DeeplinkService } from '../../../shared/service/deeplink.service';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'app-bos-modal',
  templateUrl: './app-bos-modal.component.html'
})
export class AppBosModalComponent {
  @Output() closeModal = new EventEmitter<string>();

  constructor(
    private deeplinkService: DeeplinkService,
    private gaService: GaService
  ) {}

  handleClose() {
    this.gaService.addEvent('transition_modal_click_tetap_disini', 'transition', '');
    this.closeModal.emit();
  }

  openApp() {
    this.gaService.addEvent('transition_modal_click_buka_bos', 'transition', '');
    this.closeModal.emit();
    this.deeplinkService.deeplink();
  }
}
