import {Pipe, PipeTransform} from "@angular/core";

@Pipe({
  name: "libCurrency"
})
export class LibCurrencyPipe implements PipeTransform {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  transform(value: number, short: boolean = true, digits: number = 3, label: string = "Rp", maxValue: number = 10000000): unknown {
    if (value === undefined || value === null) {
      return label + '0';
    }
    if (value < 1) {
      return label + '0';
    }
    if (short) {
      const formattedValue = value.toLocaleString('ID');
      if (value < maxValue) {
        return label + value.toLocaleString("ID");
      } else {
        const decimalSeparator = Number("1.2").toLocaleString().substr(1, 1);
        const num = formattedValue.split(decimalSeparator);
        const first = num[0];
        let last = num[1]?.split("")[0];

        if (last) {
          last = last.replace(/0+$/, '');
          last = last.length === 0 ? '0' : last;
        } else {
          last = '0';
        }

        const lookup = [
          { value: 1e6, symbol: "jt" },
          { value: 1e9, symbol: "m" },
          { value: 1e12, symbol: "t" }
        ];
        const item = lookup.slice().reverse().find((item) => {
          return value >= item.value;
        });
        return item ? label + first + '.' + last + item.symbol : label + formattedValue;
      }
    } else {
      if (value) {
        return label + value.toLocaleString("ID");
      } else {
        return label + 0
      }
    }
  }

}
