import { Injectable } from '@angular/core';
import { Apollo } from 'apollo-angular';
import { ListApiTypes } from '../../../types/list-api.types';
import {
  BOSResponseCheckEligibleVoucherDto,
  CheckEligibilityVoucherDto,
  CheckEligibleVoucherDto,
  ListEligibleVoucherInput,
  ListEligibleVoucherResponse,
  SCMResponseCheckEligibilityVoucherDto,
  SCMResponseListVoucherDto,
  TotalEligibleVoucher
} from './voucher.dto';
import { ApolloQueryResult } from '@apollo/client';
import { Observable } from 'rxjs';
import {
  checkEligibilityVoucher,
  listEligibleVoucher,
  listVoucher,
  totalEligibleVoucher,
  checkEligibleVoucherSubs,
  listEligibleVoucherSubscription
} from './voucher.constant';

@Injectable({
  providedIn: 'root'
})
export class VoucherService {
  constructor(private apollo: Apollo) {}

  checkEligibilityVoucher(
    data: CheckEligibilityVoucherDto
  ): Observable<ApolloQueryResult<SCMResponseCheckEligibilityVoucherDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<SCMResponseCheckEligibilityVoucherDto>({
        fetchPolicy: 'no-cache',
        query: checkEligibilityVoucher,
        variables: data
      });
  }
  checkEligibleVoucher(
    data: CheckEligibleVoucherDto
  ): Observable<ApolloQueryResult<BOSResponseCheckEligibleVoucherDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<BOSResponseCheckEligibleVoucherDto>({
        fetchPolicy: 'no-cache',
        query: checkEligibleVoucherSubs,
        variables: data
      });
  }

  listVoucher(): Observable<ApolloQueryResult<SCMResponseListVoucherDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<SCMResponseListVoucherDto>({
        fetchPolicy: 'no-cache',
        query: listVoucher
      });
  }
  listEligibleVoucher(): Observable<
    ApolloQueryResult<ListEligibleVoucherResponse>
  > {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ListEligibleVoucherResponse>({
        fetchPolicy: 'no-cache',
        query: listEligibleVoucher
      });
  }
  listEligibleVoucherSubscription(
    data: ListEligibleVoucherInput
  ): Observable<ApolloQueryResult<ListEligibleVoucherResponse>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ListEligibleVoucherResponse>({
        fetchPolicy: 'no-cache',
        query: listEligibleVoucherSubscription,
        variables: data
      });
  }
  totalEligibleVoucher(): Observable<ApolloQueryResult<TotalEligibleVoucher>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<TotalEligibleVoucher>({
        fetchPolicy: 'no-cache',
        query: totalEligibleVoucher
      });
  }
}
