import { gql } from 'apollo-angular';

export const listAvailablePayment = gql`
  query {
    listAvailablePayment {
      paymentMethodCategories {
        name
        displayName
        paymentMethods {
          name
          displayName
          logo
        }
      }
    }
  }
`;

export const createPayment = gql`
  mutation createPayment($createPaymentInput: CreatePaymentInput!) {
    createPayment(createPaymentInput: $createPaymentInput) {
      id
      paymentCode
      transactionRefId
      transactionAmount
      paymentType
      expiredAt
      paymentFee
      issuerName
      paymentStatus
    }
  }
`;

export const retryPayment = gql`
  mutation retryPayment($retryPaymentInput: RetryPaymentInput!) {
    retryPayment(retryPaymentInput: $retryPaymentInput) {
      id
      paymentCode
      transactionRefId
      transactionAmount
      paymentType
      expiredAt
      paymentFee
      issuerName
      paymentStatus
    }
  }
`;
