import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";

@Component({
  selector: "lib-qty-cart-input",
  templateUrl: "./qty-cart-input.component.html",
  styleUrls: ["./qty-cart-input.component.scss"]
})
export class QtyCartInputComponent implements OnInit {
  @Input() isButtonDisable = false;
  @Input() valueQuantity = 1;
  @Input() quantity = 0;

  @Input() set qty(data: number) {
    this.quantity = data
    this.checkQuantity()
  }

  @Input() minQuantity = 1;
  @Input() width = "auto";
  @Input() height = "100%";
  @Output() qtyChange = new EventEmitter<number>();
  @Output() deleteCart: EventEmitter<string> = new EventEmitter();
  disableMin = false;
  disablePlus = false;
  errorMassage = false;

  ngOnInit() {
    if (this.minQuantity === 0) {
      this.minQuantity = 1
    }

    if (!this.quantity) {
      this.disableMin = true;
      this.disablePlus = true;
    } else {
      this.checkQuantity()
    }
  }

  onInput(event: Event): void {
    const inputValue = (event.target as HTMLInputElement).value;
    const numericValue = parseFloat(inputValue.replace(/[^\d]/g, ''));
    if (numericValue < 1 || !numericValue) {
      this.valueQuantity = 1;
      this.checkQuantity();
      this.qtyChange.emit(this.valueQuantity);
    } else if (numericValue >= this.quantity) {
      this.errorMassage = true;
      this.valueQuantity = this.quantity;
      this.checkQuantity();
      this.qtyChange.emit(this.valueQuantity);
    } else {
      this.valueQuantity = numericValue;
      this.checkQuantity();
      this.qtyChange.emit(this.valueQuantity);
    }
  }

  reduceQuantity() {
    this.valueQuantity = this.valueQuantity - 1;
    this.checkQuantity();
    this.qtyChange.emit(this.valueQuantity);
  }

  addQuantity() {
    this.valueQuantity = this.valueQuantity + 1;
    this.checkQuantity();
    this.qtyChange.emit(this.valueQuantity);
  }

  checkQuantity() {
    this.disableMin = false;
    this.disablePlus = false;
    if (this.valueQuantity <= (this.minQuantity ?? 1)) {
      this.disableMin = true;
    }
    if (this.valueQuantity >= this.quantity) {
      this.disablePlus = true;
      this.errorMassage = true;
    }
  }

  handleDeleteCart() {
    this.deleteCart.emit()
  }

}