import { Injectable } from '@angular/core';
import { Apollo, MutationResult } from 'apollo-angular';
import {
  CreateLoginInputDto, RequestValidateAutoLoginKeyDto,
  ResponseLogintDto, ResponseValidateAutoLoginKeyDto,
} from './login.dto';
import { Observable } from 'rxjs';
import {loginMutation, validateAutoLoginKey} from './login.constant';
import { ListApiTypes } from '../../../types/list-api.types';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  constructor(private apollo: Apollo) {}

  createLoginMutation(
    data: CreateLoginInputDto
  ): Observable<MutationResult<ResponseLogintDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseLogintDto>({
        mutation: loginMutation,
        variables: data
      });
  }


  validateAutoLoginKeyMutation(
    data: RequestValidateAutoLoginKeyDto
  ): Observable<MutationResult<ResponseValidateAutoLoginKeyDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseValidateAutoLoginKeyDto>({
        mutation: validateAutoLoginKey,
        variables: data
      });
  }

}


