import {Injectable} from '@angular/core';
import {Apollo, MutationResult} from "apollo-angular";
import {Observable} from "rxjs";
import {
  CreateAgentInputDto,
  PinAuthorizationInputDto,
  RequestListItemDto,
  ResponseCheckKycAgentDto,
  ResponseCreateAgentDto,
  ResponseListItemSubDto,
  ResponsePinAuthorizationDto
} from "./ajak-cuan.dto";
import {ListApiTypes} from "../../../types/list-api.types";
import {checkKycAgent, createAgent, listItemAjakCuanSCM, pinAuthorization} from "./ajak-cuan.constant";
import {ApolloQueryResult} from "@apollo/client";

@Injectable({
    providedIn: 'root'
})
export class AjakCuanService {

    constructor(private apollo: Apollo) {
    }

    createAgent(
        data: CreateAgentInputDto
    ): Observable<MutationResult<ResponseCreateAgentDto>> {
        return this.apollo.use(ListApiTypes.BOS_API_GRAPHQL_URL)
            .mutate<ResponseCreateAgentDto>({
                mutation: createAgent,
                variables: data,
            })
    };

    checkKycAgent(): Observable<ApolloQueryResult<ResponseCheckKycAgentDto>> {
        return this.apollo.use(ListApiTypes.BOS_API_GRAPHQL_URL)
            .query<ResponseCheckKycAgentDto>({
                fetchPolicy: 'no-cache',
                query: checkKycAgent,
            })
    }

    listItem(data: RequestListItemDto): Observable<ApolloQueryResult<ResponseListItemSubDto>> {
        return this.apollo.use(ListApiTypes.BOS_API_GRAPHQL_URL)
            .query<ResponseListItemSubDto>({
                fetchPolicy: 'no-cache',
                query: listItemAjakCuanSCM,
                variables: data
            })
    }

  pinAuthorization(data: PinAuthorizationInputDto): Observable<MutationResult<ResponsePinAuthorizationDto>> {
      return this.apollo.use(ListApiTypes.BOS_API_GRAPHQL_URL)
        .mutate<ResponsePinAuthorizationDto>({
          mutation: pinAuthorization,
          variables: data,
        })
  }
}
