import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Router} from '@angular/router';
import {
  animate,
  state,
  style,
  transition,
  trigger
} from '@angular/animations';
import {
  CriteriaProgressionStatusType,
  CriteriaType,
  OperatorType,
  TierProgramType,
  UserTierStatusType
} from '../../../types/enums.type';
import {
  TierPrograms,
  UserTierDetail
} from '../../../graphql/bos-api/membership/membership.dto';

@Component({
  selector: 'lib-tier-card',
  templateUrl: './tier-card.component.html',
  styleUrls: ['./tier-card.component.scss'],
  animations: [
    trigger('fadeInOut', [
      state('expanded', style({height: '*', opacity: 1})),
      state('collapsed', style({height: '0', opacity: 0})),
      transition('expanded <=> collapsed', animate('0.3s ease-in-out'))
    ])
  ]
})
export class TierCardComponent implements OnInit {
  @Input() dataTier: UserTierDetail | null = null;
  @Input() position!: number;
  @Input() status!: UserTierStatusType;
  @Input() dataTierPrograms!: TierPrograms[];
  isChecked = false;
  isExpand = false;
  isExpandAnd = false;
  @Output() handleCheck: EventEmitter<boolean> = new EventEmitter();

  /** this for condition for submit IOS */
  showButtonBeli = false

  backgroundColor = {
    fromColor: 'rgba(174, 137, 83, 1)',
    toColor: 'rgba(234, 197, 143, 1)',
    circleColor: 'rgba(174, 137, 83, .2)'
  };

  transactionTotalPercentage!: number[];
  transactionAmountPercentage!: number[];
  currentIndex = 0;
  expandedStates: Map<string, boolean> = new Map();

  constructor(private router: Router) {
  }

  ngOnInit(): void {
    if (this.dataTier) {
      this.calculateProgress();
    }
  }

  get Enums() {
    return {
      CriteriaType,
      TierProgramType,
      OperatorType,
      UserTierStatusType
    };
  }

  calculateProgress() {
    if (this.dataTier) {
      if (this.dataTier.tier.name === 'basic') {
        const filterByType = ['SUBSCRIPTION_PLAN'];
        const filterByTypeSet = new Set(filterByType);
        this.dataTierPrograms = this.dataTierPrograms.filter((item) => {
          return item.criterias.some(({type}) => {
            return filterByTypeSet.has(type);
          });
        });
      } else {
        this.dataTierPrograms = this.dataTierPrograms.map((tierProgram) => {
          const criterias = tierProgram.criterias.map((item) => {
            if (item.criteriaProgression) {
              const transactionTotalPercentage = this.calculatePercentage(
                item.criteriaProgression.totalTransaction,
                item.totalTransaction
              );
              const transactionAmountPercentage = this.calculatePercentage(
                item.criteriaProgression.totalPrice,
                item.totalPrice
              );

              const isChecked =
                item.criteriaProgression.status ===
                CriteriaProgressionStatusType.SUCCESS;

              return {
                ...item,
                transactionTotalPercentage,
                transactionAmountPercentage,
                isChecked
              };
            }
            return {
              ...item
            };
          });
          return {
            ...tierProgram,
            criterias
          };
        });
      }
    }
  }

  handleMoreItem(tierProgram: TierPrograms) {
    const key = tierProgram.id;
    const currentState = this.expandedStates.get(key) || false;
    this.expandedStates.set(key, !currentState);
    this.isExpand = !this.isExpand;
  }

  handleMoreItemAnd(tierProgram: TierPrograms) {
    const key = tierProgram.id;
    const currentState = this.expandedStates.get(key) || false;
    this.expandedStates.set(key, !currentState);
    this.isExpandAnd = !this.isExpandAnd;
  }

  calculatePercentage(current: number, total: number) {
    if (total == 0) {
      return 0;
    }
    const result = (current / total) * 100;
    return result > 100 ? 100 : result;
  }

  onCheck() {
    this.handleCheck.emit();
  }

  goToSubscription() {
    this.router.navigate(['/subscription/list-subscription']);
  }
}
