import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'lib-subs-status-card',
  templateUrl: './subs-status-card.component.html'
})
export class SubsStatusCardComponent implements OnInit {
  @Input() endDate = '';
  @Input() image = '';
  @Input() name = '';
  @Input() planId = '';
  @Input() dateData = new Date();
  @Input() showButton = true;
  isExpired = false;
  nowDate = new Date();
  dayLeft = 0;

  constructor(private router: Router) {}

  ngOnInit() {
    this.handleDateFunc();
  }

  handleDateFunc() {
    this.dateData = new Date(this.endDate);
    const timeDiff = Math.abs(this.dateData.getTime() - this.nowDate.getTime());
    this.dayLeft = Math.ceil(timeDiff / (1000 * 3600 * 24));
    this.isExpired = this.dateData.getTime() < this.nowDate.getTime();
  }

  goToListPackage() {
    this.router.navigate(['/subscription/list-subscription'], {
      queryParams: { id: this.planId }
    });
  }
}
