import {
  AfterContentInit,
  AfterViewInit,
  Component,
  ComponentFactoryResolver,
  ContentChild,
  ElementRef,
  Input,
  ViewContainerRef
} from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { ControlMessageComponent } from './control-message.component';

@Component({
  selector: '[formControlStyle]',
  template: '<ng-content></ng-content>',
})
export class FormControlStyleDirective
  implements AfterViewInit, AfterContentInit
{
  static VALID_STYLE = 'has-success';
  static INVALID_STYLE = 'has-danger';

  @ContentChild('hasMessage', { read: ViewContainerRef, static: false })
  other: any;
  componentRef: any;
  @Input('formControlStyle') public formGroup!: FormGroup;
  @Input('formComponent') public formComponent = '';
  @Input('customErrorMessage') public customErrorMessage!: string;
  private component!: FormControl;
  private inputElement: any;

  constructor(
    private el: ElementRef,
    private viewContainerRef: ViewContainerRef,
    private componentFactoryResolver: ComponentFactoryResolver
  ) {}

  ngAfterViewInit(): void {
    let componentName: string;
    let isInputMask: any;

    this.inputElement = null;

    if (this.other) {
      this.formComponent = this.other._hostTNode.value;
    }

    if (this.formComponent !== '') {
      this.inputElement = this.el.nativeElement.querySelector(
        this.formComponent
      );
    } else {
      this.inputElement = this.el.nativeElement.querySelector('input');
    }

    if (this.inputElement) {
      componentName = this.inputElement.getAttribute('formControlName');
      isInputMask = this.inputElement.getAttribute('inputMask');
    }

    // @ts-ignore
    if (!componentName) {
      console.error(
        'FormControlStyleDirective: Unable to get the control name. Is the formControlName attribute set correctly?'
      );
      return;
    }

    const control = this.formGroup.get(componentName);
    if (!(control instanceof FormControl)) {
      console.error(
        `FormControlStyleComponent: Unable to get the FormControl from the form and the control name: ${componentName}.`
      );
      return;
    }
    this.component = control as FormControl;

    this.component.statusChanges.subscribe((status) => {
      if (
        isInputMask !== undefined &&
        isInputMask !== null &&
        this.component.touched
      ) {
        this.component.markAsDirty();
      }
      this.onStatusChange(status, this.component.dirty);
    });
    this.onStatusChange(this.component.status, this.component.dirty);
  }

  ngAfterContentInit() {
    // Dynamically create a control message and attach it to the component.
    setTimeout(() => {
      if (this.other) {
        const factory = this.componentFactoryResolver.resolveComponentFactory(
          ControlMessageComponent
        );
        this.componentRef = this.other.createComponent(factory);
        const instance = this.componentRef.instance;
        instance.formGroup = this.formGroup;
        instance.tag = this.formComponent;
        instance.customErrorMessage = this.customErrorMessage;
      }
    }, 1);
  }

  onStatusChange(status: string, dirty: boolean): void {
    const cl = this.el.nativeElement.classList;

    if (status === 'VALID' && dirty) {
      cl.add(FormControlStyleDirective.VALID_STYLE);
      cl.remove(FormControlStyleDirective.INVALID_STYLE);
    } else if (status === 'INVALID' && dirty) {
      cl.add(FormControlStyleDirective.INVALID_STYLE);
      cl.remove(FormControlStyleDirective.VALID_STYLE);
    } else {
      cl.remove(FormControlStyleDirective.VALID_STYLE);
      cl.remove(FormControlStyleDirective.INVALID_STYLE);
    }
  }

  checkComponent(elementName: string): any {
    if (elementName) {
      const getType = elementName.split('-');
      return getType[0];
    }
  }
}
