import { Component } from '@angular/core';
import {PlatformTypeService} from "../../../service/platform-type.service";

@Component({
  selector: 'mobile-container',
  templateUrl: './mobile-container.component.html'
})
export class MobileContainerComponent {
  isMobile = false
  isTablet = false
  constructor(
    private platformTypeService: PlatformTypeService,
  ) {
    this.isMobile = this.platformTypeService.checkIsMobile()
    this.isTablet = this.platformTypeService.checkIsTablet()
  }
}
