import {Inject, Injectable, PLATFORM_ID} from "@angular/core";
import {isPlatformBrowser} from "@angular/common";

@Injectable({
  providedIn: "root"
})
export class EventData {
  isReady = false;

  constructor(@Inject(PLATFORM_ID) private platformId: object) {
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
    }
  }

  /** popupModal */
  set dateBanner(date: string) {
    if (this.isReady) {
      localStorage.setItem("dateBanner", date)
    }
  }

  get dateBanner(): string {
    if (this.isReady) {
      const date = localStorage.getItem("dateBanner")
      return date ? date : ""
    } else {
      return ""
    }
  }

  set registerExpired(date: string) {
    if (this.isReady) {
      localStorage.setItem('registerExpiredAt', date)
    }
  }

  get registerExpired(): string {
    if (this.isReady) {
      const date = localStorage.getItem("registerExpiredAt")
      return date ? date : ""
    } else {
      return ""
    }
  }
}
