  constructor(@Inject(PLATFORM_ID) private platformId: object) {
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
    }
  }

  /** TOKEN */
  set token(data: string) {
    if (this.isReady) {
      localStorage.setItem("access_token", data);
    }
  }

  get token(): string {
    if (this.isReady) {
      const token = localStorage.getItem("access_token") as string;
      return token;
    } else {
      return "";
    }
  }

  /** EXPIRED */
  set expiredDate(data: string) {
    if (this.isReady) {
      localStorage.setItem("expiredDate", data);
    }
  }

  get expiredDate(): string {
    if (this.isReady) {
      const date = localStorage.getItem("expiredDate");
      return date ? date : "";
    } else {
      return "";
    }
  }

