  /** IS DEFAULT ADDRESS */
  set isDefaultAddress(data: boolean) {
    if (this.isReady) {
      localStorage.setItem("isDefaultAddress", String(data));
      this.dataIsDefaultAddress = data;
    }
  }

  get isDefaultAddress(): boolean {
    if (this.isReady) {
      if (this.dataIsDefaultAddress) {
        return this.dataIsDefaultAddress;
      } else {
        const defaultAddress = localStorage.getItem(
          "isDefaultAddress"
        ) as string;
        if (defaultAddress) {
          this.dataIsDefaultAddress = defaultAddress == "true" ? true : false;
          return this.dataIsDefaultAddress;
        } else {
          return false;
        }
      }
    } else {
      return false;
    }
  }

  /** detailLocation */
  set detailLocation(data: DetailLocation) {
    if (this.isReady) {
      localStorage.setItem("detailLocation", JSON.stringify(data));
    }
  }

  get detailLocation(): DetailLocation {
    if (this.isReady) {
      const local = localStorage.getItem("detailLocation");
      if (local) {
        const defaultAddress: DetailLocation = JSON.parse(
          local
        ) as DetailLocation;
        return defaultAddress;
      } else {
        return {} as DetailLocation;
      }
    } else {
      return {} as DetailLocation;
    }
  }

  /** detailLocation */
  set location(data: Address) {
    this.address = data;
    if (this.isReady) {
      localStorage.setItem("location", JSON.stringify(data));
    }
  }

  get location(): Address {
    if (this.isReady) {
      const local = localStorage.getItem("location");
      if (local) {
        const defaultAddress: Address = JSON.parse(local) as Address;
        return defaultAddress;
      } else {
        return {} as Address;
      }
    } else {
      return {} as Address;
    }
  }

