export enum PaymentType {
  VA = 'VA',
  QRIS = 'QRIS',
  VOUCHER = 'VOUCHER',
  PAY_ON_DELIVERY = 'PAY_ON_DELIVERY'
}

export enum PaymentMethodType {
  VA = 'virtual_account',
  QR = 'qr_code',
  EWALLET = 'e-wallet',
  POD = 'pay-on-delivery'
}

export interface ListAvailablePaymentInput {
  listAvailablePaymentInput: {
    filter: {
      paymentType: PaymentType;
    };
  };
}

export interface ResponseListAvailablePayment {
  listAvailablePayment: ListAvailablePayment;
}

export interface ListAvailablePayment {
  paymentMethodCategories: PaymentMethodCategory[]
}

export interface PaymentMethodCategory {
  name: PaymentMethodType
  displayName: string
  paymentMethods: PaymentMethod[]
}

export interface PaymentMethod {
  name: string
  displayName: string
  logo: string
}

export interface SCMPayment {
  id: string
  paymentCode: string
  paymentCodeType: string
  transactionRefId: string
  transactionAmount: number
  paymentType: string
  expiredAt: string
  paymentFee: number
  paymentStatus: string
  issuerName: string
}

export interface CreatePaymentInput {
  createPaymentInput: {
    paymentMethod: PaymentMethodType;
    transactionRefId: string;
    transactionSupplierInvoice: string;
  }
}
export interface ResponseCreatePaymentDto {
  createPayment: SCMPayment
}

export interface RetryPaymentInput {
  retryPaymentInput: {
    paymentMethod: PaymentMethodType;
    transactionRefId: string;
    transactionSupplierInvoice: string;
  }
}
export interface ResponseRetryPaymentDto {
  retryPayment: SCMPayment
}
