import { Injectable } from '@angular/core';
import { ApolloQueryResult } from '@apollo/client';
import { ListApiTypes } from '../../../types/list-api.types';
import { Apollo } from 'apollo-angular';
import { Observable } from 'rxjs';
import { listBusinessCategory } from './business-category.constant';
import { ResponseListBusinessCategoryDto } from './business-category.dto';

@Injectable({
  providedIn: 'root'
})
export class BusinessCategoryService {
  constructor(private apollo: Apollo) {}

  listBusinessCategory(): Observable<
    ApolloQueryResult<ResponseListBusinessCategoryDto>
  > {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseListBusinessCategoryDto>({
        query: listBusinessCategory
      });
  }
}
