import { Injectable } from "@angular/core";
import { Apollo, MutationResult } from "apollo-angular";
import { ApolloQueryResult } from "@apollo/client";

import { Observable } from "rxjs";

import {
    listHistoryBalanceInputDto,
    ResponseListHistoryBalanceDto,
    ResponseGetAffiliateBalanceDto,
    WithDrawCommissionInputDto,
    ResponseWithDrawCommissionDto
} from "./balance.dto"

import {
    getAffiliateBalance,
    listHistoryBalance,
    withDrawCommission,
} from "./balance.constant"

import { ListApiTypes } from "../../../types/list-api.types";

@Injectable({
    providedIn: "root"
})

export class BalanceService {
    constructor(
        private apollo: Apollo
    ) { }

    listHistoryBalance(
        data: listHistoryBalanceInputDto
    ): Observable<ApolloQueryResult<ResponseListHistoryBalanceDto>> {
        return this.apollo
            .use(ListApiTypes.BOS_API_GRAPHQL_URL)
            .query<ResponseListHistoryBalanceDto>({
                query: listHistoryBalance,
                variables: data,
                fetchPolicy: "no-cache"
            })
    }

    getAffiliateBalance(): Observable<ApolloQueryResult<ResponseGetAffiliateBalanceDto>> {
        return this.apollo
            .use(ListApiTypes.BOS_API_GRAPHQL_URL)
            .query<ResponseGetAffiliateBalanceDto>({
                query: getAffiliateBalance,
                fetchPolicy: "no-cache"
            });
    }

    withDrawCommission(
        data: WithDrawCommissionInputDto
    ): Observable<MutationResult<ResponseWithDrawCommissionDto>> {
        return this.apollo
            .use(ListApiTypes.BOS_API_GRAPHQL_URL)
            .mutate<ResponseWithDrawCommissionDto>({
                mutation: withDrawCommission,
                variables: data
            });
    }

}
