import { gql } from 'apollo-angular';

export const detailProductVariant = gql`
  query detailProductVariant(
    $detailProductVariantInput: DetailProductVariantInput!
  ) {
    detailProductVariant(
      detailProductVariantInput: $detailProductVariantInput
    ) {
      id
      name
      priceBeforeDiscount
      discount
      discountType
      price
      unitType
      unitQuantity
      totalUnitQuantity
      isSold
      isActive
      stock
      product {
        description
        image
        name
        isPODAvailable
        productVariants {
          name
        }
        supplier {
          id
          name
          image
          slug
          tier {
            id
            name
            displayName
            image
          }
        }
        isActive
      }
    }
  }
`;

export const listProductVariant = gql`
  query listProductVariant($listProductVarian: ListProductVariantInput!) {
    listProductVariant(listProductVariant: $listProductVarian) {
      page
      limit
      total
      totalPage
      productVariants {
        id
        name
        priceBeforeDiscount
        price
        unitType
        unitQuantity
        totalUnitQuantity
        discount
        discountType
        isActive
        stock
        product{
          id
          isPODAvailable
          productVariants {
            name
          }
          name
          image
          description
          supplier {
            name
            image
          }
        }
        slug
      }
    }
  }
`;

export const listProduct = gql`
  query listProduct($listProductInput: ListProductInput!, $referralCode: String, $warehouseId: ID!) {
    listProduct(listProductInput: $listProductInput) {
      page
      limit
      total
      totalPage
      products {
        id
        name
        image
        slug
        description
        actualPriceMin
        actualPriceMax
        isActive
        isPODAvailable
        warehouse{
          id
          name
        }
        supplier {
          id
          name
          tier {
            id
            name
          }
        }
        activeDiscount(warehouseId:$warehouseId){
          id
          name
          thirdPartyCode
          code
          type
        }
        productVariants {
          id
          slug
          name
          price
          stock
          discountAmount
          unitType
          activeDiscounts(warehouseId: $warehouseId){
            id
            thirdPartyDiscountId
            mixCode
            minQuantity
            minUom
            type
            value
            lineNumber
          }
          isAffiliate(referralCode: $referralCode)
        }
      }
    }
  }
`;

export const detailProduct = gql`
  query detailProduct($detailProductInput: DetailProductInput!, $referralCode: String, $warehouseId: ID!) {
    detailProduct(detailProductInput: $detailProductInput) {
      id
      name
      description
      image
      slug
      isPODAvailable
      isAvailable
      activeDiscount(warehouseId: $warehouseId){
        id
        name
        code
        type
      }
      productVariants {
        id
        name
        price
        discountAmount
        stock
        unitType
        unitQuantity
        totalUnitQuantity
        slug
        isAffiliate(referralCode: $referralCode)
        activeDiscounts(warehouseId: $warehouseId){
          id
          thirdPartyDiscountId
          sku
          mixCode
          minQuantity
          minUom
          type
          value
          lineNumber
        }
        productVariantWarehouses {
          id
          status
          price
          discountAmount
          finalPrice
        }
      }
      supplier {
        id
        name
        image
        slug
        tier {
          id
          name
          displayName
          image
        }
      }
      warehouse {
        id
        name
        slug
      }
      isActive
    }
  }
`;
