export enum SCMBannerType {
  INTERNAL,
  EXTERNAL
}

export interface IListBannerResponse {
  id: string;
  image: string;
  url: string;
  type: string;
  position: number;
}

export interface IResponseListBannerDto {
  listBanner: IListBannerResponse[];
}

export interface RequestListBannerDto {
  listBannerInput: ListBannerInput
}

export interface ListBannerInput {
  platform: 'AFFILIATE' | 'SCM'
}

export interface ListBannerConfigurationInput {
  location: LocationListBanner | string;
  type: TypeListBanner | string;
  platform: FlatformBanner | string;
}

export enum LocationListBanner {
  HOME,
  AJAK_CUAN,
  UNSPECIFIED
}

export enum TypeListBanner {
  SLIDER = 'SLIDER',
  POPUP = 'POPUP'
}

export enum FlatformBanner {
  BOS,
  POS
}

export enum TargetBanner {
  TARGET_ALL,
  TARGET_MANUAL
}

export enum DisplayWhenBanner {
  ALL,
  LOGIN,
  NO_LOGIN
}

export interface ListBannersResponseDto {
  type: string;
  position: number;
  image: string;
  url: string;
  name: string;
  linkWeb: string;
  linkMobile: string;
}

export interface BannerConfigurations {
  id: string;
  type: TypeListBanner;
  platform: FlatformBanner;
  location: LocationListBanner;
  status: string;
  target: TargetBanner;
  displayWhen: DisplayWhenBanner;
  startDate: string;
  endDate: string;
  displayFrequent: number;
  displayCycle: number;
  name: string;
  banners: ListBannersResponseDto[];
}

export interface ResponseListBannerConfigurationDto {
  listBannerConfiguration: {
    bannerConfigurations: BannerConfigurations[];
  }
}

export interface ListBannerConfigurationInputDto {
  listBannerConfigurationInput: ListBannerConfigurationInput;
}

