import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
  name: "CalculatePriceAdjustment"
})
export class CalculatePriceAdjustmentPipe implements PipeTransform {

  transform(value: number, type: string, subtotal: number): number {
    let total = 0;
    if (type === 'PERCENTAGE') {
      total = (value / 100) * subtotal;
    } else {
      total = value;
    }
    return total;
  }

}
