import { Injectable } from '@angular/core';
import {Apollo, MutationResult} from 'apollo-angular';
import { Observable } from 'rxjs';
import {ResponseGetProfileDto, ResponseUpdateUserDto, UpdateUserDto,ResponseLogoutDto} from './profile.dto';
import { ListApiTypes } from '../../../types/list-api.types';
import {detailUser, updateUser, logout} from './profile.constant';
import {ApolloQueryResult} from "@apollo/client";

@Injectable({
  providedIn: 'root'
})
export class ProfileService {

  constructor(private apollo: Apollo) {}

  getProfile(): Observable<ApolloQueryResult<ResponseGetProfileDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .query<ResponseGetProfileDto>({
        fetchPolicy: 'no-cache',
        query: detailUser,
      });
  };

  updateUser(data: UpdateUserDto): Observable<MutationResult<ResponseUpdateUserDto>> {
    return this.apollo.use(ListApiTypes.BOS_API_GRAPHQL_URL).mutate<ResponseUpdateUserDto>({
      mutation: updateUser,
      variables: data
    })
  };

  logout(): Observable<MutationResult<ResponseLogoutDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseLogoutDto>({
        mutation: logout
      });
  }

}
