import { Component, EventEmitter, Input, Output } from '@angular/core';
import { LibCurrencyPipe } from '../../../pipe/lib-currency.pipe';
import { CartErrorCodeEnum } from '../../../types/enums.type';

@Component({
  selector: 'lib-cart-item-card',
  templateUrl: './cart-item-card.component.html'
})
export class CartItemCardComponent {
  @Input() checked = false;
  @Input() image = '';
  @Input() name = '';
  @Input() variantName = '';
  @Input() category = '';
  @Input() price = 0;
  @Input() priceBeforeDiscount = 0;
  @Input() discount = 0;
  @Input() discountType = '';
  @Input() qty = 0;
  @Input() minQty = 0;
  @Input() stock = 0;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  @Input() errors: any[] = [];
  @Input() errorCart = false;
  @Input() pod = false;
  @Input() referral = '';
  @Input() isPromo = false;
  @Input() discountAmount = 0;
  @Input() tierName = '';
  @Input() subTotal = 0;
  @Input() discIMType = '';
  @Input() discIMValue = 0;
  @Output() checkUncheck = new EventEmitter<boolean>();
  @Output() qtyChange = new EventEmitter<number>();
  @Output() deleteClick = new EventEmitter<string>();

  constructor(private currencyPipe: LibCurrencyPipe) { }

  onCheckboxChange() {
    this.checkUncheck.emit();
  }

  addReduceQty($qty: number) {
    this.qtyChange.emit($qty);
  }

  handleDelete() {
    this.deleteClick.emit();
  }

  replaceDiscount(value: number, type: string) {
    let result = '';
    if (type === 'FIXED' || type === '') {
      result = `hemat ${this.currencyPipe.transform(value, false)}`;
    }
    if (type === 'PERCENTAGE') {
      result = `hemat ${value}%`;
    }
    return result;
  }

  replaceDiscountIndomarco(value: number, type: string) {
    let result = '';
    if (type === 'FIXED' || type === '') {
      result = `hemat ${this.currencyPipe.transform(value, false)}`;
    }
    if (type === 'PERCENTAGE') {
      result = `hemat ${value}%`;
    }
    return result;
  }

  calculationShowPriceAfterDiscount(value: number, type: string) {
    let result = 0;
    if (type === 'FIXED') {
      result = this.price - this.discIMValue;
    } else if (type === 'PERCENTAGE') {
      const initialValue = this.price;
      const percentage = this.discIMValue;
      const discount = (percentage / 100) * initialValue;
      result = initialValue - discount;
    }

    return this.roundNominal(result);
  }

  get Enums() {
    return {
      CartErrorCodeEnum
    };
  }

  checkErrorStockOff() {
    this.errors.find((item) => {
      if (
        item.code === CartErrorCodeEnum.OUT_OF_STOCK ||
        item.code === CartErrorCodeEnum.INSUFFICIENT_STOCK
      ) {
        return true;
      } else {
        return false;
      }
    });
  }

  checkError() {
    if (
      this.errors.length === 0 ||
      this.errors[0].code === CartErrorCodeEnum.PRICE_HAS_CHANGED ||
      this.errors[0].code === CartErrorCodeEnum.INSUFFICIENT_STOCK
    ) {
      if (this.errorCart) {
        return true;
      } else {
        return false;
      }
    } else {
      return true;
    }
  }

  roundNominal(nominal: number): number {
    const [integerPart, decimalPart] = nominal
      .toString()
      .split('.')
      .map(Number);
    if (!decimalPart) {
      return integerPart;
    }

    const significantDecimal = parseFloat(
      '0.' + decimalPart.toString().slice(0, 2)
    );

    if (significantDecimal > 0.5) {
      return Math.ceil(nominal);
    }

    if (significantDecimal < 0.4) {
      return Math.floor(nominal);
    }

    return integerPart + significantDecimal;
  }
}
