import { Inject, Injectable } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { DOCUMENT } from '@angular/common';
import { environment } from '../../../environments/environment';

export interface MetaTagInterface {
  title: string;
  description: string;
  image?: string;
  robots?: string;
  canonical?: string;
}

@Injectable({
  providedIn: 'root'
})
export class MetaService {
  constructor(
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    @Inject(DOCUMENT) private doc: any,
    private title: Title,
    private metaTagService: Meta
  ) {}

  setMeta(data: MetaTagInterface) {
    /** title */
    if (data.title) {
      this.title.setTitle(data.title);
      this.metaTagService.updateTag({ name: 'title', content: data.title });
      this.metaTagService.updateTag({
        name: 'twitter:title',
        content: data.title
      });
      this.metaTagService.updateTag({
        property: 'og:title',
        content: data.title
      });
    } else {
      this.metaTagService.removeTag("name='title'");
      this.metaTagService.removeTag("name='twitter:title'");
      this.metaTagService.removeTag("property='og:title'");
    }

    /** description */
    if (data.description) {
      this.metaTagService.updateTag({
        name: 'description',
        content: data.description
      });
      this.metaTagService.updateTag({
        property: 'og:description',
        content: data.description
      });
      this.metaTagService.updateTag({
        property: 'twitter:description',
        content: data.description
      });
    } else {
      this.metaTagService.removeTag("name='description'");
      this.metaTagService.removeTag("property='og:description'");
      this.metaTagService.removeTag("property='twitter:description'");
    }

    /** image */
    if (data.image) {
      this.metaTagService.updateTag({
        property: 'og:image',
        content: data.image
      });
      this.metaTagService.updateTag({
        name: 'twitter:image',
        content: data.image
      });
    } else {
      this.metaTagService.removeTag("property='og:image'");
      this.metaTagService.removeTag("name='twitter:image'");
    }

    if (environment.ENV === 'PRODUCTION') {
      if (data.robots) {
        this.metaTagService.updateTag({ name: 'robots', content: data.robots });
      } else {
        this.metaTagService.updateTag({
          name: 'robots',
          content: 'follow,index'
        });
      }
    } else {
      this.metaTagService.updateTag({ name: 'robots', content: 'noindex' });
    }

    /** global */
    this.metaTagService.updateTag({ name: 'og:type', content: 'website' });
    this.metaTagService.updateTag({
      name: 'og:site_name',
      content: 'Youtap BOS'
    });

    this.metaTagService.updateTag({
      name: 'twitter:card',
      content: 'summary_large_image'
    });
    this.metaTagService.updateTag({
      name: 'twitter:site',
      content: '@youtapid'
    });
    this.metaTagService.updateTag({
      name: 'twitter:creator',
      content: '@youtapid'
    });

    const element: HTMLLinkElement[] = this.doc.querySelectorAll(
      `link[rel="canonical"]`
    );
    if (element.length > 0) {
      element[0].parentNode?.removeChild(element[0]);
    }
    const link: HTMLLinkElement = this.doc.createElement('link');
    link.setAttribute('rel', 'canonical');
    this.doc.head.appendChild(link);
    const el = this.doc.createElement('a');
    el.href = this.doc.URL;
    const fullUrl = 'https://bos.youtap.id' + el.pathname + el.search;
    link.setAttribute('href', fullUrl);
    this.metaTagService.updateTag({
      name: 'og:url',
      content: fullUrl
    });
  }
}
