import { Injectable } from '@angular/core';
import { Apollo, MutationResult } from 'apollo-angular';
import { AddDeliveryCourierInput, DetailDeliveryInputDto, ListCourierCategoryInput, ListDeliveryCourierInput, ResponseAddDeliveryCourierDto, ResponseDetailDeliveryDto, ResponseListCourierCategoryDto, ResponseListDeliveryCourierDto } from './delivery.dto';
import { Observable } from 'rxjs';
import { ApolloQueryResult } from '@apollo/client';
import { ListApiTypes } from '../../../types/list-api.types';
import { addDeliveryCourier, detailDelivery, listCourierCategory, listDeliveryCourier } from './delivery.constant';

@Injectable({
  providedIn: 'root'
})
export class DeliveryService {

  constructor(private apollo: Apollo) { }

  detailDelivery(
    data: DetailDeliveryInputDto
  ): Observable<ApolloQueryResult<ResponseDetailDeliveryDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseDetailDeliveryDto>({
        query: detailDelivery,
        variables: data
      });
  }

  listCourierCategory(
    data: ListCourierCategoryInput
  ): Observable<ApolloQueryResult<ResponseListCourierCategoryDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListCourierCategoryDto>({
        query: listCourierCategory,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  listDeliveryCourier(
    data: ListDeliveryCourierInput
  ): Observable<ApolloQueryResult<ResponseListDeliveryCourierDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListDeliveryCourierDto>({
        query: listDeliveryCourier,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  addDeliveryCourier(
    data: AddDeliveryCourierInput
  ): Observable<MutationResult<ResponseAddDeliveryCourierDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseAddDeliveryCourierDto>({
        mutation: addDeliveryCourier,
        variables: data
      });
  }
}
