import {Component, EventEmitter, forwardRef, Input, Output} from '@angular/core';
import {NG_VALUE_ACCESSOR} from "@angular/forms";

@Component({
  selector: 'lib-area-input',
  templateUrl: './area-input.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => AreaInputComponent),
      multi: true
    }
  ],
  styleUrls: ['./area-input.component.scss']
})
export class AreaInputComponent {
  value = '';
  @Output() valueChange: EventEmitter<string> = new EventEmitter<string>();
  @Input() isError = false;
  @Input() width = '';
  @Input() height = 'h-[65px]';
  @Input() rounded = '!rounded-[8px]';
  @Input() textSize = '!text-[14px]';
  @Input() placeholder = '';
  @Input() maxLength = '255';
  @Input() isDisabled = false;

  onInput(event: Event) {
    const target = event.target as HTMLInputElement;
    this.value = target.value;
    this.valueChange.emit(this.value);
  }

  onChange: (data: string) => void = () => { return ''; };
  onTouch: (data: string) => void = () => { return '' };

  changeDrop(data: string) {
    this.onChange(data);
    this.onTouch(data);
    this.valueChange.emit(this.value);
  }

  // this method sets the value programmatically
  writeValue(value: string) {
    this.value = value;
  }

  // upon UI element value changes, this method gets triggered
  registerOnChange(fn: (_: string) => void) {
    this.onChange = fn;
  }

// upon touching the element, this method gets triggered
  registerOnTouched(fn: (_: string) => void) {
    this.onTouch = fn;
  }
}
