import { gql } from 'apollo-angular';

export const generateProductStockReport = gql`
  query generateProductStockReport(
    $generateProductStockReportInput: GenerateProductStockReportInput!
  ) {
    generateProductStockReport(
      generateProductStockReportInput: $generateProductStockReportInput
    ) {
      status
    }
  }
`;

