import { Injectable } from '@angular/core';
import { ApolloQueryResult } from '@apollo/client';
import { Apollo } from 'apollo-angular';
import { Observable } from 'rxjs';
import { getSubscriptionInvoice } from './invoice.constant';
import {
  GetSubscriptionInput,
  ResponseGetSubscriptionDto
} from './invoice.dto';
import { ListApiTypes } from '../../../types/list-api.types';

@Injectable({
  providedIn: 'root'
})
export class InvoiceService {
  constructor(private apollo: Apollo) {}

  downloadSubsInv(
    data: GetSubscriptionInput
  ): Observable<ApolloQueryResult<ResponseGetSubscriptionDto>> {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<ResponseGetSubscriptionDto>({
        query: getSubscriptionInvoice,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }
}
