import { Component, OnInit } from "@angular/core";
import { Modal } from "../../../shared/service/modal/modal.model";
import {
  Address,
  AddressService,
  DeleteAddressInput,
  ListAddressDto,
  LocationApiService,
  ResponseListAddressDto,
  SetDefaultAddressInput,
  RequestDetailAddressDto
} from "@youtap-web-lib";
import { Router } from "@angular/router";
import { AuthData } from "../../../shared/data/auth.data";

@Component({
  selector: "list-address-modal",
  templateUrl: "./list-address-modal.component.html",
  styleUrls: ["./list-address-modal.component.scss"]
})
export class ListAddressModalComponent extends Modal implements OnInit {
  searchValue = "";
  selectedId = "";
  deletedId = "";
  loading = true;
  loadingDefault = false;
  listAddress!: ResponseListAddressDto;
  confirmDelete = false;

  constructor(
    private addressService: AddressService,
    private locationApiService: LocationApiService,
    private router: Router,
    public authData: AuthData
  ) {
    super();
  }

  ngOnInit(): void {
    this.getListAddress();
  }

  // eslint-disable-next-line @typescript-eslint/no-empty-function
  onInjectInputs(): void {
  }

  getListAddress() {
    this.loading = true;
    const data: ListAddressDto = {
      listAddressInput: {
        page: 0,
        limit: 25,
        search: {
          keyword: this.searchValue
        }
      }
    };
    this.addressService.listAddress(data).subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.listAddress = data;
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  setDefaultAddress() {
    this.loadingDefault = true;
    const data: SetDefaultAddressInput = {
      id: this.selectedId
    };
    this.addressService.setDefaultAddress(data).subscribe({
      next: () => {
        this.setCoverage();
      },
      error: () => {
        this.loadingDefault = false;
      }
    });
  }

  deleteAddress() {
    const data: DeleteAddressInput = {
      id: this.deletedId
    };
    this.addressService.deleteAddress(data).subscribe({
      next: () => {
        this.confirmDelete = !this.confirmDelete;
        this.deletedId = "";
        this.getListAddress();
      }
    });
  }

  editAddress(id: string) {
    this.dismiss({});
    this.router.navigate(["/address/edit"], {
      queryParams: { id }
    });
  }

  selectDeletedId(id: string) {
    this.deletedId = id;
    this.confirmDelete = !this.confirmDelete;
  }

  selectAddress(item: Address) {
    this.selectedId = item.id;
  }

  action(value: string): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({});
  }

  setCoverage() {
    const data: RequestDetailAddressDto = {
      id: this.selectedId
    };
    this.addressService.detailAddress(data).subscribe({
      next: () => {
        // this.authData.isCoverageArea = data.detailAddress.isCoverageArea;
        // this.authData.village = data.detailAddress.village;
        // const districtName = data.detailAddress.district.name;
        // const regencyName = data.detailAddress.regency.name;
        // this.authData.coverageArea = districtName + ", " + regencyName;
        // this.authData.isDefaultAddress = true;
        this.action("use");
        this.loadingDefault = false;
      },
      error: () => {
        this.loadingDefault = false;
      }
    });

    // this.locationApiService
    //   .detailLocation(location)
    //   .toPromise()
    //   .then(({ data }: any) => {
    //
    //   });
  }
}
