import { gql } from 'apollo-angular';

export const detailMerchantRegistrationUser = gql`
  query detailMerchantRegistrationUser{
    detailMerchantRegistrationUser{
      accountId
      accountType
      referralCode
      addressDetail{
        id
        description
        districtId
        merchantKycId
        name
        postalCodeId
        provinceId
        regencyId
        villageId
        latitude
        longitude
      }
      bankAccount{
        accountNumber
        id
        name
        status
        bankId
      }
      personalInformation{
        id
        email
        identityCardImage
        identityCardNumber
        merchantKycId
        name
        referralCode
        selfieIdentityCardImage
        signatureImage
        taxIdentityCardNumber
        urlWebsite
      }
    }
  }
`

export const createAddressMutation = gql`
  mutation ($createAddressDetailInput: CreateAddressDetailInput!) {
    createAddressDetail(createAddressDetailInput: $createAddressDetailInput) {
      id
    }
  }
`;
export const createPersonalInfoMutation = gql`
  mutation ($createPersonalInformationInputV2: CreatePersonalInformationInputV2!) {
    createPersonalInformation(
      createPersonalInformationInputV2: $createPersonalInformationInputV2
    ) {
      id
      email
      identityCardImage
      identityCardNumber
      merchantKycId
      name
      referralCode
      selfieIdentityCardImage
      signatureImage
      taxIdentityCardNumber
      urlWebsite
    }
  }
`;
