import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'libCurrencyNew'
})
export class LibCurrencyNewPipe implements PipeTransform {
  private formatNumber(value: number): string {
    return value?.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  }

  transform(value: number, label: string = "Rp", maxValue: number = 10000000): string {
    if (value < maxValue) {
      return label + ' ' + this.formatNumber(value);
    } else if (value >= 1e12) {
      return label + ' ' + (value / 1e12).toFixed(1) + 't';
    } else if (value >= 1e9) {
      return label + ' ' + (value / 1e9).toFixed(1) + 'm';
    } else if (value >= 1e6) {
      return label + ' ' + (value / 1e6).toFixed(1) + 'jt';
    } else {
      return label + ' ' + this.formatNumber(value);
    }
  }
}
