import { Component } from '@angular/core';
import { Modal } from '../../../service/modal/modal.model';
import { InputModalDto } from '../../../service/modal/modal.service';
import { CommonModule } from '@angular/common';

@Component({
  standalone: true,
  selector: 'modal-header',
  template: ` <div
    class="header-modal flex flex-row justify-between items-center z-10 relative px-4">
    <div class="text-[16px] text-grey-1 font-medium">{{ title }}</div>
    <div class="cursor-pointer">
      <img
        *ngIf="closeButton"
        (click)="cancel()"
        src="./assets/images/png/close.png"
        width="15px"
        class="m-auto"
        alt="close" />
    </div>
    <!--      <i *ngIf="closeButton" class="pi pi-times cursor-pointer" (click)="cancel()"></i>-->
  </div>`,
  imports: [CommonModule]
})
export class ModalHeaderComponent extends Modal {
  title = '';
  closeButton = true;

  onInjectInputs(inputs: InputModalDto): void {
    this.title = inputs.title;
    this.closeButton = inputs.closeButton;
  }

  save(): void {
    this.close('saving');
  }

  cancel(): void {
    this.dismiss('canceling');
  }
}
