import { Injectable } from "@angular/core";
import { ApolloQueryResult } from "@apollo/client";
import { ListApiTypes } from '../../../types/list-api.types';
import { Apollo } from "apollo-angular";
import { Observable } from "rxjs";

import {
  SupplierDetailIdDto,
  ResponseSupplierDetailDto,
  ListSupplierDto,
  ResponseListSupplierDto, IsSupplierCoveredDto, ResponseIsSupplierCoveredDto
} from "./supplier.dto";
import {detailSupplier, isSupplierCovered, listSupplier} from "./supplier.constant";

@Injectable({
    providedIn: 'root'
})
export class SupplierService{
    constructor(private apollo: Apollo){}

    detailSupplier(data:SupplierDetailIdDto):Observable<ApolloQueryResult<ResponseSupplierDetailDto>>{
        return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseSupplierDetailDto>({
            query: detailSupplier,
            variables: data
        });
    }

    listSupplier(data: ListSupplierDto): Observable<ApolloQueryResult<ResponseListSupplierDto>> {
        return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseListSupplierDto>({
          query: listSupplier, variables: data,
        })
    }

    isSupplierCovered(data: IsSupplierCoveredDto): Observable<ApolloQueryResult<ResponseIsSupplierCoveredDto>> {
        return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseIsSupplierCoveredDto>({
          fetchPolicy: "no-cache",
          query: isSupplierCovered,
          variables: data
        })
    }
}
