import { gql } from "apollo-angular";

export const listAddress = gql`
  query listAddress($listAddressInput: ListAddressInput!) {
    listAddress(listAddressInput: $listAddressInput) {
      total
      page
      totalPage
      addresses {
        id
        name
        postalCode
        addressDetail
        recipientName
        recipientPhone
        recipientEmail
        province {
          id
          name
        }
        regency {
          id
          name
        }
        district {
          id
          name
        }
        village {
          id
          name
        }
        latitude
        longitude
        isDefault
        createdAt
        updatedAt
      }
    }
  }
`;

