import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TermTypes } from '../../../types/term.types';

@Component({
  selector: 'lib-subs-package-card',
  templateUrl: './subs-package-card.component.html'
})
export class SubsPackageCardComponent {
  @Input() name = 'Extra Mantab 3 bulan';
  @Input() description = 'Paket Youtap';
  @Input() price = 10000;
  @Input() term = 3;
  @Input() termType = TermTypes.MONTH;
  @Input() image = '';
  @Input() discount = 0;
  @Input() normalPrice = 0;
  @Input() discountType = 'SHORTCURRENCY';
  @Input() isAffiliate = false;
  @Output() clickDetail: EventEmitter<void> = new EventEmitter();

  getDetailSubs() {
    this.clickDetail.emit();
  }
}
