import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ProductVariantWarehouse } from '../../../graphql/supply-chain-public-api/product/product.dto';

@Component({
  selector: 'lib-promo-product-card',
  templateUrl: './promo-product-card.component.html',
  styleUrls: ['./promo-product-card.component.scss']
})
export class PromoProductCardComponent implements OnInit {
  @Input() promoData!: ProductVariantWarehouse;
  @Input() maxLength = 30;
  @Output() clickProduct: EventEmitter<void> = new EventEmitter();
  @Input() isPOD = false;
  discountType = 'FIXED';
  priceBeforeDiscount = 0;

  ngOnInit(): void {
    const image = this.promoData.productVariant.product.image.replace('s0', 's256');
    this.promoData.productVariant.product.image = image;
  }

  onClickProduct() {
    this.clickProduct.emit();
  }

  checkDiscount(productVariant: ProductVariantWarehouse) {

    this.priceBeforeDiscount =
      productVariant.finalPrice + productVariant.discountAmount;
    if (this.priceBeforeDiscount > productVariant.finalPrice) {
      return true;
    } else {
      return false;
    }
  }
}
