import { gql } from 'apollo-angular';

export const detailPayment = gql`
  query detailPayment($detailPaymentInput: DetailPaymentInput!) {
    detailPayment(detailPaymentInput: $detailPaymentInput) {
      id
      issuerName
      paymentFee
      paymentCode
      paymentCodeType
      transactionRefId
      transactionAmount
      paymentStatus
      expiredAt
      paymentType
    }
  }
`;
