import { Injectable } from '@angular/core';
import { ApolloQueryResult } from '@apollo/client';
import { Apollo } from 'apollo-angular';
import { Observable } from 'rxjs';
import { ListApiTypes } from '../../../types/list-api.types';
import { detailVillage, listVillage } from './village.constant';
import { DetailVillageDto, ListVillageDto, ResponseDetailVillageDto, ResponseListVillageDto } from './village.dto';

@Injectable({
  providedIn: 'root'
})
export class VillageService {

  constructor(private apollo: Apollo) { }

  listVillage(data: ListVillageDto): Observable<ApolloQueryResult<ResponseListVillageDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseListVillageDto>({
      query: listVillage,
      variables: data
    })
  }

  detailVillage(data: DetailVillageDto): Observable<ApolloQueryResult<ResponseDetailVillageDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseDetailVillageDto>({
      query: detailVillage,
      variables: data
    })
  }
}
