import { Injectable } from '@angular/core';
import { Apollo, MutationResult } from 'apollo-angular';
import {
  BOSCreateTransactionInput,
  CompareTransactionInput,
  GenerateTransactionReportInput,
  GraphTransactionInput,
  IssuerPercentageInput,
  LatestTransactionInput,
  listPaymentIssuerResponse,
  ListProductAnalyticInput,
  ListTransactionInputDto,
  ResponseCheckStatusReportDto,
  ResponseCompareTransactionDto,
  ResponseCreateSubscriptionTransactionDto,
  ResponseGenerateTransactionReportDto,
  ResponseGraphTransactionDto,
  ResponseIssuerPercentageDto,
  ResponseLatestTransactionDto,
  ResponseListProductAnalyticsDto,
  ResponseListTransactionsDto,
  ResponseSummaryTransactionDto,
  ResponseTransactionUpdatedAtDto,
  SummaryTransactionInputDto,
  TransactionDetailDto
} from './transaction.dto';
import { Observable } from 'rxjs';
import { ApolloQueryResult } from '@apollo/client';
import {
  checkStatusReport,
  compareTransaction,
  createSubscriptionTransaction,
  generateTransactionReport,
  graphTransaction,
  issuerPercentage,
  latestTransaction,
  listPaymentIssuer,
  listProductAnalytics,
  listTransaction,
  summaryTransaction,
  transactionDetail,
  transactionUpdatedAt
} from './transaction.constant';
import { ListApiTypes } from '../../../types/list-api.types';

@Injectable({
  providedIn: 'root'
})
export class TransactionService {
  constructor(private apollo: Apollo) {}

  compareTransaction(
    data: CompareTransactionInput
  ): Observable<ApolloQueryResult<ResponseCompareTransactionDto>> {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<ResponseCompareTransactionDto>({
        query: compareTransaction,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  latestTransaction(
    data: LatestTransactionInput
  ): Observable<ApolloQueryResult<ResponseLatestTransactionDto>> {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<ResponseLatestTransactionDto>({
        query: latestTransaction,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  transactionUpdatedAt(): Observable<
    ApolloQueryResult<ResponseTransactionUpdatedAtDto>
  > {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<ResponseTransactionUpdatedAtDto>({
        query: transactionUpdatedAt,
        fetchPolicy: 'no-cache'
      });
  }

  listProductAnalytics(
    data: ListProductAnalyticInput
  ): Observable<ApolloQueryResult<ResponseListProductAnalyticsDto>> {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<ResponseListProductAnalyticsDto>({
        query: listProductAnalytics,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  issuerPercentage(
    data: IssuerPercentageInput
  ): Observable<ApolloQueryResult<ResponseIssuerPercentageDto>> {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<ResponseIssuerPercentageDto>({
        query: issuerPercentage,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  graphTransaction(
    data: GraphTransactionInput
  ): Observable<ApolloQueryResult<ResponseGraphTransactionDto>> {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<ResponseGraphTransactionDto>({
        query: graphTransaction,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  listTransaction(
    data: ListTransactionInputDto
  ): Observable<ApolloQueryResult<ResponseListTransactionsDto>> {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<ResponseListTransactionsDto>({
        query: listTransaction,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  checkStatusReport(): Observable<
    ApolloQueryResult<ResponseCheckStatusReportDto>
  > {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<ResponseCheckStatusReportDto>({
        query: checkStatusReport,
        fetchPolicy: 'no-cache'
      });
  }

  generateTransactionReport(
    data: GenerateTransactionReportInput
  ): Observable<ApolloQueryResult<ResponseGenerateTransactionReportDto>> {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<ResponseGenerateTransactionReportDto>({
        query: generateTransactionReport,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  summaryTransaction(
    data: SummaryTransactionInputDto
  ): Observable<ApolloQueryResult<ResponseSummaryTransactionDto>> {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<ResponseSummaryTransactionDto>({
        query: summaryTransaction,
        variables: data,
        fetchPolicy: 'no-cache'
      });
  }

  listPaymentIssuer(): Observable<
    ApolloQueryResult<listPaymentIssuerResponse>
  > {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<listPaymentIssuerResponse>({
        query: listPaymentIssuer
      });
  }

  transactionDetail(
    id: string
  ): Observable<ApolloQueryResult<TransactionDetailDto>> {
    return this.apollo
      .use(ListApiTypes.POS_API_GRAPHQL_URL)
      .query<TransactionDetailDto>({
        query: transactionDetail,
        variables: { id },
        fetchPolicy: 'no-cache'
      });
  }

  createSubscriptionTransaction(
    data: BOSCreateTransactionInput
  ): Observable<MutationResult<ResponseCreateSubscriptionTransactionDto>> {
    return this.apollo
      .use(ListApiTypes.BOS_API_GRAPHQL_URL)
      .mutate<ResponseCreateSubscriptionTransactionDto>({
        mutation: createSubscriptionTransaction,
        variables: data
      });
  }
}
