import { gql } from 'apollo-angular';

export const listSection = gql`
  query listSection {
    listSection {
      sections {
        id
        type
        name
        warehouse {
          id
          name
          slug
          supplier(imageResolution: S) {
            id
            name
            image
            slug
            tier {
              id
              name
              displayName
              image
            }
          }
        }
        categories(imageResolution: S) {
          id
          image
          slug
          name
          categoryBranches {
            id
            categoryParent {
              id
              name
              slug
              image
            }
          }
        }
        banners {
          id
          image
          position
          url
          type
        }
        officialPartners {
          id
          name
          slug
          supplier(imageResolution: S) {
            id
            image
          }
        }
        highlight {
          id
          name
          displayName
          description
          themeColor
          highlightProductVariantWarehouses {
            id
            productVariantWarehouse {
              id
              price
              discountAmount
              finalPrice
              productVariant {
                id
                name
                slug
                unitType
                unitQuantity
                totalUnitQuantity
                product {
                  id
                  name
                  slug
                  image
                  price
                  description
                  isPODAvailable
                }
              }
            }
          }
        }
      }
    }
  }
`;

export const listHighlight = gql`
  query listHighlight($listHighlightInput: ListHighlightInput!) {
    listHighlight(listHighlightInput: $listHighlightInput) {
      page
      limit
      total
      totalPage
      highlights {
        id
        name
        displayName
        description
        themeColor
        highlightProductVariantWarehouses {
          id
          productVariantWarehouse {
            id
            price
            discountAmount
            productVariant {
              id
              name
              slug
              unitType
              unitQuantity
              totalUnitQuantity
              product {
                id
                name
                slug
                image
                price
                description
                isPODAvailable
              }
            }
          }
        }
      }
    }
  }
`;

export const listHighlightProductVariantWarehouse = gql`
  query listHighlightProductVariantWarehouse($listHighlightProductVariantInput: ListHighlightProductVariantWarehouseInput!) {
    listHighlightProductVariantWarehouse(listHighlightProductVariantInput: $listHighlightProductVariantInput) {
      page
      limit
      total
      totalPage
      highlightProductVariantWarehouses {
        productVariantWarehouse {
          id
          price
          finalPrice
          discountAmount
          stock
          productVariant {
            id
            name
            slug
            unitType
            unitQuantity
            totalUnitQuantity
            product {
              id
              name
              slug
              image
              price
              description
              isPODAvailable
            }
          }
        }
      }
    }
  }
`
