import { Injectable } from "@angular/core";
import { ApolloQueryResult } from "@apollo/client";
import { ListApiTypes } from '../../../types/list-api.types';
import { Apollo } from "apollo-angular";
import { Observable } from "rxjs";

import { ListCoverageAreaInput, ResponseCoverageAreaDto } from "./coverage.dto";
import { listCoverageArea } from "./coverage.constant";

@Injectable({
    providedIn: 'root'
})
export class CoverageAreaService{
    constructor(private apollo: Apollo){}

    listCoverageArea(data:ListCoverageAreaInput):Observable<ApolloQueryResult<ResponseCoverageAreaDto>>{
        return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseCoverageAreaDto>({
            query: listCoverageArea,
            variables: data
        });
    }
}
