import { gql } from 'apollo-angular';

export const getWarehouse = gql`
  query getWarehouse(
    $detailWarehouseBySupplierInput: DetailWarehouseBySupplierInput!
  ) {
    getWarehouse(
      detailWarehouseBySupplierInput: $detailWarehouseBySupplierInput
    ) {
      id
      name
      postalCode
      addressDetail
      phone
      email
      isDefault
      latitude
      longitude
      province {
        id
        name
      }
      regency {
        id
        name
      }
      district {
        id
        name
      }
      village {
        id
        name
      }
      operationalHours {
        id
        day
        openTime
        closeTime
        isOpen
      }
    }
  }
`;

export const listWarehouse = gql`
  query listWarehouse($listWarehouseInput: ListWarehouseInput!) {
    listWarehouse(listWarehouseInput: $listWarehouseInput) {
      page
      limit
      total
      totalPage
      warehouses {
        id
        name
        slug
        operationalHours {
          day
          openTime
          closeTime
          isOpen
        }
        supplier {
          id
          name
          slug
          image
          status
          tier {
            id
            name
            displayName
            image
          }
        }
      }
    }
  }
`;

export const detailWarehouse = gql`
  query detailWarehouse($detailWarehouseInput: DetailWarehouseInput!) {
    detailWarehouse(detailWarehouseInput: $detailWarehouseInput) {
      id
      name
      email
      phone
      slug
      minPurchase
      supplier {
        id
        name
        image
         email
      phone
        cutoffTime
        tierId
        slug
        phone
        email
        tier {
          id
          name
          displayName
          image
        }
      }
      operationalHours {
          day
          openTime
          closeTime
          isOpen
        }
    }
  }
`;
