import { gql } from 'apollo-angular';

export const listCashier = gql`
  query listCashier($listCashierInput: ListCashierInput!) {
    listCashier(listCashierInput: $listCashierInput) {
      cashiers {
        userId
        name
        startDate
        endDate
        intialCash
        endCash
        totalSales
        difference
      }
      limit
      page
      total
      totalPage
    }
  }
`;

export const getUMCashier = gql`
  query getUMCashier($getUMCashierInput: GetUMCashierInput!) {
    getUMCashier(getUMCashierInput: $getUMCashierInput) {
      id
      name
    }
  }
`;

export const summaryCashier = gql`
  query summaryCashier($summaryCashierInput: SummaryCashierInput!) {
    summaryCashier(summaryCashierInput: $summaryCashierInput) {
      totalCash
      totalCashOut
      totalSales
      lastUpdatedAt
    }
  }
`;

export const detailCashier = gql`
  query detailCashier($detailCashierInput: DetailCashierInput!) {
    detailCashier(detailCashierInput: $detailCashierInput) {
      endingAt
      startAt
      completedTransaction
      summary {
        cash
        nonCash
        initialCash
        endingCash
        tax
        completedTransactionPHP
        openBillNotPaid
        PHPNotPaid
        totalOpenBillNotPaid
        totalPHPNotPaid
        totalPriceAdjustmentReduction
        totalPriceAdjustmentAddition
        differenceCash
        totalSales
        totalEndCash
      }
      paymentMethod {
        creditCard
        debitCard
        goFood
        grabFood
        vaBri
        vaMandiri
        vaBca
        vaBina
        vaBni
        transfer
        qris
        cash
        others
      }
      products {
        name
        quantity
        addOn {
          name
        }
      }
      cashExpenses {
        id
        cashBookId
        price
        title
        note
        createdAt
        updatedAt
        deletedAt
      }
      user {
        id
        name
        email
        phone
        level
        userStatus
        isLogin
        isActive
        isVerified
        createdAt
        updatedAt
        nik
        roles {
          name
          displayName
        }
      }
      cashBooks {
        id
        userId
        initialCash
        endingCash
        countNotPaid
        status
      }
    }
  }
`;

export const generateCashierReport = gql`
  query generateCashierReport($generateCashierInput: GenerateCashierInput!) {
    generateCashierReport(generateCashierInput: $generateCashierInput) {
      status
    }
  }
`;
