export interface Inbox {
  id: string;
  applicationId: string;
  broadcastName: string;
  category: CategoryInboxType;
  eventType: InboxEventType;
  event: string;
  referenceId: string;
  link: string;
  title: string;
  imageUrl: string;
  shortDescription: string;
  description: string;
  status: string;
  createdAt: Date;
  updatedAt: Date;
}

export interface UserInbox {
  id: string;
  isRead: boolean;
  inbox: Inbox;
}

export interface ListUserInboxResponse {
  limit: number;
  page: number;
  total: number;
  totalPage: number;
  metadata: {
    unread: number
  }
  userInboxes: UserInbox[];
}

export interface ResponseListUserInboxDto {
  listUserInbox: ListUserInboxResponse;
}

export interface ListUserInboxInputDto {
  listUserInboxInput: ListUserInboxInput;
}

export interface ListUserInboxInput {
  page: number;
  limit: number;
  filter?: FilterInboxInput;
}

export interface FilterInboxInput {
  interval?: RangeInboxType;
  withCounted?: boolean;
}

export enum RangeInboxType {
  ALL = "all",
  DAY_7 = "DAY_7",
  DAY_14 = "DAY_14"
}

export enum CategoryInboxType {
  ALL = "ALL",
  CAMPAIGN = "INBOX_CATEGORY_CAMPAIGN",
  PROMO = "INBOX_CATEGORY_PROMO",
  REMINDER = "INBOX_CATEGORY_REMINDER",
  INFO = "INBOX_CATEGORY_INFO"
}

export enum InboxEventType {
  INTERNAL = "INBOX_EVENT_TYPE_INTERNAL",
  EXTERNAL = "INBOX_EVENT_TYPE_EXTERNAL"
}

export interface ReadUnreadUserInboxInput {
  ids: string[];
  isRead: boolean;
}

export interface ReadUnreadUserInboxInputDto {
  readUnreadUserInboxInput: ReadUnreadUserInboxInput;
}

export interface ReadUnreadUserInboxResponse {
  userInboxes: UserInbox[];
}

export interface ResponseReadUnreadUserInboxDto {
  readUnreadUserInbox: ReadUnreadUserInboxResponse;
}

export interface ReadUnreadAllUserInboxInput {
  isRead: boolean;
}

export interface ReadUnreadAllUserInboxInputDto {
  readUnreadAllUserInboxInput: ReadUnreadAllUserInboxInput;
}

export interface ReadUnreadAllUserInboxResponse {
  success: boolean;
}

export interface ResponseReadUnreadAllUserInboxDto {
  readUnreadAllUserInbox: ReadUnreadAllUserInboxResponse;
}
