import { AfterViewInit, Component, OnInit } from "@angular/core";
import { NavigationEnd, Router } from "@angular/router";
import { AuthData } from "../../../data/auth.data";
import { WindowActionService } from "../../../service/window-action.service";

@Component({
  selector: "menu-footer",
  templateUrl: "./menu-footer.component.html"
})
export class MenuFooterComponent implements OnInit, AfterViewInit {
  token = "";
  currentUrl = "/";

  constructor(
    private router: Router,
    private windowActionService: WindowActionService,
    private authData: AuthData) {
  }

  ngOnInit(): void {
    this.currentUrl = this.windowActionService.getPathName();
    this.router.events.subscribe((val) => {
      if (val instanceof NavigationEnd) {
        this.currentUrl = this.windowActionService.getPathName();
      }
    });
  }

  ngAfterViewInit() {
    this.token = this.authData.token;
  }
}
