import { Directive, ElementRef, Input, OnInit, Renderer2 } from "@angular/core";

@Directive({
  // eslint-disable-next-line @angular-eslint/directive-selector
  selector: '[appBusyIf]'
})
export class BusyIfDirective implements OnInit{
  busyIf!: boolean;
  div = document.createElement('div');
  divSpiner = document.createElement('div');
  tagName: any;
  @Input('appBusyIf') set setBusy(status: boolean){
    this.tagName = this.el.nativeElement.tagName;
    // if button
    if(this.tagName.toLowerCase() === 'button'){
      if(status){
        // this.render.setProperty(this.el.nativeElement,'disabled','true');
        this.el.nativeElement.disabled = true;
        this.render.addClass(this.div,'busyIf-btn-overlay-loader');
      }else{
        this.el.nativeElement.disabled = false;
        // this.render.setProperty(this.el.nativeElement,'disabled','false');
        this.render.removeClass(this.div,'busyIf-btn-overlay-loader');
      }
    }
    else if(status){
      this.busyIf = true;
      this.render.addClass(this.div,'busyIf-overlay')
      this.render.addClass(this.divSpiner,'busyIf-loader')
      // this.render.appendChild(this.el.nativeElement.parentNode,this.div)
    }else{
      this.busyIf = false;
      this.render.removeClass(this.div,'busyIf-overlay')
      this.render.removeClass(this.divSpiner,'busyIf-loader')
    }
  };

  constructor(
    public el: ElementRef,
    public render: Renderer2,
  ) { }

  ngOnInit(){
    this.setByTagName();
  }

  setByTagName(){
    this.tagName = this.el.nativeElement.tagName;

    // if button
    if(this.tagName.toLowerCase() === 'button'){
      this.render.addClass(this.el.nativeElement,'busyIf-parent');
      this.render.appendChild(this.el.nativeElement,this.div);
    }else{
      this.render.addClass(this.el.nativeElement.parentNode,'busyIf-parent');
      this.render.appendChild(this.el.nativeElement.parentNode,this.div);
      this.render.appendChild(this.el.nativeElement.parentNode,this.divSpiner);
    }
  }

}
