import { Component, Input, Output, EventEmitter } from '@angular/core';
import { TIcon, TPosition } from './basic-button.dto';

@Component({
  selector: 'lib-basic-button',
  templateUrl: './basic-button.component.html'
})
export class BasicButtonComponent {
  @Input() title = '';
  @Input() width = '';
  @Input() maxWidth = '';
  @Input() height = '';
  @Input() textSize = 'text-sm';
  @Input() iconSize = 'w-6';
  @Input() bColor = '!bg-blue';
  @Input() borderColor = 'primary-yt-blue';
  @Input() outlined = false;
  @Input() textColor = '!text-primary-yt-blue';
  @Input() disabled = false;
  @Input() iconPosition: TPosition = 'left';
  @Input() icon: TIcon = '';
  @Input() border = '';
  @Input() padding = '';
  @Input() paddingX = '';
  @Input() paddingY = '';
  @Input() rounded = '10px';
  @Input() marginTitle = 'mx-2';
  @Input() justifyContent = '!justify-center';
  @Input() leading = '';
  @Input() borderSize = '!border-2';
  @Input() fontBold = '!font-normal';

  @Output() handleEmit = new EventEmitter<string>();

  handleClick() {
    this.handleEmit.emit();
  }
}
