import { gql } from "apollo-angular";

export const getAffiliateBalance = gql`
    query getAffiliateBalance {
        getAffiliateBalance {
            balance
        }
    }
`;

export const listHistoryBalance = gql`
    query listHistoryBalance($listHistoryBalanceInput: ListHistoryBalanceInput!){
        listHistoryBalance(listHistoryBalanceInput: $listHistoryBalanceInput){
            balances{
                id
                debit
                credit
                balance
                agentId
                title
                description
                transactionItemId
                balanceBefore
                createdAt
                updatedAt
            }
            page
            limit
            total
            totalPage
        }
    }
`;

export const withDrawCommission = gql`
    mutation withDrawCommission($withDrawCommissionInput: WithDrawCommissionInput!){
        withDrawCommission(withDrawCommissionInput: $withDrawCommissionInput){
            id
            debit
            credit
            balance
            agentId
            title
            description
            transactionItemId
            balanceBefore
            createdAt
        }
    }
`;