import { Injectable } from "@angular/core";
import { AuthData } from "../data/auth.data";
import {InboxService} from "../../../../../../libs/youtap-web-lib/src/lib/graphql/bos-api/inbox/inbox.service";
import {ListUserInboxInput} from "../../../../../../libs/youtap-web-lib/src/lib/graphql/bos-api/inbox/inbox.dto";

@Injectable({
  providedIn: "root"
})
export class CountInboxService {
  public countInbox = 0;

  constructor(
    private inboxService: InboxService,
    private authData: AuthData
  ) {
  }

  getCountInbox() {
    if (this.authData.token) {
      const listUserInboxInput: ListUserInboxInput = {
        page: 1,
        limit: 5,
        filter: {
          withCounted: true
        }
      };
      this.inboxService.listUserInbox({ listUserInboxInput }).subscribe({
        next: ({ data }) => {
          this.countInbox = data.listUserInbox.metadata.unread;
        },
      });
    }
  }
}
