import { gql } from 'apollo-angular';

export const createCart = gql`
  mutation ($createCartInput: CreateCartInput!) {
    createCart(createCartInput: $createCartInput) {
      id
    }
  }
`;

export const listCart = gql`
  query listCart {
    listCart {
      id
      warehouse {
        id
        name
        slug
        minPurchase
        supplier {
          id
          name
          image
          tier {
            id
            name
            displayName
            image
          }
        }
      }
      cartItems {
        id
        stock
        referralCode
        productVariantWarehouse {
          id
          price
          finalPrice
          discountAmount
          productVariant {
            id
            name
            minQuantity
            maxQuantity
            totalUnitQuantity
            stock
            product {
              id
              name
              image
              description
              price
              categoryProducts {
                category {
                  name
                }
              }
              isPODAvailable
            }
          }
        }
        quantity
        subtotal
        discountAmount
        thirdPartyDiscountProduct{
          type
          value
        }
        isSelected
        errors {
          code
          message
        }
      }
      deliveryCourierId
      priceAdjustments {
        id
        name
        isActive
        type
        value
        baseCalculation
      }
      note
      total
      error {
        code
        message
      }
      isPODAvailable
    }
  }
`;

