import { gql } from 'apollo-angular';

export const compareTransaction = gql`
  query compareTransaction($compareTransactionInput: CompareTransactionInput!) {
    compareTransaction(compareTransactionInput: $compareTransactionInput) {
      after {
        totalGmv {
          all
          cash
          other
          qr
        }
        trxCount {
          all
          cash
          other
          qr
        }
        totalCogs
      }
      before {
        totalGmv {
          all
          cash
          other
          qr
        }
        trxCount {
          all
          cash
          other
          qr
        }
        totalCogs
      }
    }
  }
`;

export const latestTransaction = gql`
  query latestTransaction {
    latestTransaction {
      data {
        total
        issuerName
        productCount
        createdAt
      }
    }
  }
`;

export const transactionUpdatedAt = gql`
  query transactionUpdatedAt {
    transactionUpdatedAt {
      date
    }
  }
`;

export const listProductAnalytics = gql`
  query listProductAnalytics(
    $listProductAnalyticsInput: ListProductAnalyticsInput!
  ) {
    listProductAnalytics(
      listProductAnalyticsInput: $listProductAnalyticsInput
    ) {
      page
      limit
      total
      totalPage
      data {
        price
        quantity
        subtotal
        image
        productName
        categoryName
        name
        productImage
      }
    }
  }
`;

export const issuerPercentage = gql`
  query issuerPercentage($issuerPercentageInput: IssuerPercentageInput!) {
    issuerPercentage(issuerPercentageInput: $issuerPercentageInput) {
      data {
        issuerName
        amount
        percentage
      }
    }
  }
`;

export const graphTransaction = gql`
  query graphTransaction($graphTransactionInput: GraphTransactionInput!) {
    graphTransaction(graphTransactionInput: $graphTransactionInput) {
      before {
        name
        amount
      }
      after {
        name
        amount
      }
    }
  }
`;

export const listTransaction = gql`
  query listTransaction($listTransactionInput: ListTransactionInput!) {
    listTransaction(listTransactionInput: $listTransactionInput) {
      page
      limit
      total
      totalPage
      transactions {
        id
        ytTrxId
        type
        issTrxId
        currencyCode
        subtotal
        totalBeforeTax
        total
        issuerId
        status
        createdAt
        issuerName
        productCount
      }
    }
  }
`;

export const checkStatusReport = gql`
  query checkStatusReport {
    checkStatusReport {
      data {
        id
        status
        urlDownload
        month
      }
    }
  }
`;

export const generateTransactionReport = gql`
  query generateTransactionReport(
    $generateTransactionInput: GenerateTransactionInput!
  ) {
    generateTransactionReport(
      generateTransactionInput: $generateTransactionInput
    ) {
      status
    }
  }
`;

export const summaryTransaction = gql`
  query summaryTransaction($summaryTransactionInput: SummaryTransactionInput!) {
    summaryTransaction(summaryTransactionInput: $summaryTransactionInput) {
      totalAmount {
        all
        cash
        other
        qr
        voidCash
      }
      totalTransaction {
        all
        cash
        other
        qr
        voidCash
      }
      totalCogs
      totalProfit
    }
  }
`;

export const listPaymentIssuer = gql`
  query listPaymentIssuer {
    listPaymentIssuer {
      paymentIssuers {
        id
        name
        image
        status
      }
    }
  }
`;

export const transactionDetail = gql`
  query transactionDetail($id: ID!) {
    transactionDetail(id: $id) {
      ytTrxId
      subtotal
      totalBeforeTax
      total
      tax
      taxType
      taxAmount
      rounding
      roundingType
      roundingDifference
      status
      createdAt
      updatedAt
      issuerName
      trxId
      isOnlineOrder
      transactionPriceAdjustments {
        priceAdjustmentName
        priceAdjustmentAmount
        priceAdjustmentType
        priceAdjustmentPriceType
      }
      transactionItems {
        price
        productName
        quantity
        subtotal
        image
        product {
          id
        }
        transactionDetailAddOnItems {
          addOnItemName
          addOnItemPrice
          id
          quantity
        }
      }
    }
  }
`;

export const createSubscriptionTransaction = gql`
  mutation createSubscriptionTransaction(
    $createTransactionInput: CreateTransactionInput!
  ) {
    createSubscriptionTransaction(
      createTransactionInput: $createTransactionInput
    ) {
      id
      status
      invoice
      total
      subscriptionTransactionDetails {
        id
        planDisplayName
        planBasePrice
        planOptionPrice
        subtotal
        planOptionTerm
        planOptionTermType
        planOptionType
      }
      payment {
        id
        paymentCode
        transactionRefId
        transactionAmount
        paymentType
        expiredAt
        paymentFee
        issuerName
      }
    }
  }
`;
