import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
  name: "DayFormatPipe"
})
export class DayFormatPipe implements PipeTransform {

  transform(value: number): string {
    switch (value) {
      case 0:
        return "Minggu";
        break;
      case 1:
        return "Senin";
        break;
      case 2:
        return "Selasa";
        break;
      case 3:
        return "Rabu";
        break;
      case 4:
        return "Kamis";
        break;
      case 5:
        return "Jumat";
        break;
      case 6:
        return "Sabtu";
        break;
      default:
        return "Minngu";
        break;
    }
  }

}
