import { Injectable } from "@angular/core";
import { Apollo, MutationResult } from "apollo-angular";
import {
  AddAddressDto,
  DeleteAddressInput,
  ListAddressDto, RequestDetailAddressDto,
  ResponseDefaultAddressDto,
  ResponseDeleteAddressDto, ResponseDetailAddressDto,
  ResponseListAddressDto,
  ResponseSetDefaultAddressDto,
  SetDefaultAddressInput
} from "./address.dto";
import { Observable } from "rxjs";
import { ApolloQueryResult } from "@apollo/client";
import {
  defaultAddress,
  deleteAddress,
  detailAddress,
  listAddress,
  setDefaultAddress,
  upsertAddress
} from "./address.constant";
import { ListApiTypes } from "../../../types/list-api.types";

@Injectable({
  providedIn: "root"
})
export class AddressService {
  constructor(private apollo: Apollo) {
  }

  listAddress(
    data: ListAddressDto
  ): Observable<ApolloQueryResult<ResponseListAddressDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListAddressDto>({
        query: listAddress,
        variables: data,
        fetchPolicy: "no-cache"
      });
  }

  defaultAddress(): Observable<ApolloQueryResult<ResponseDefaultAddressDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseDefaultAddressDto>({
        query: defaultAddress,
        fetchPolicy: "no-cache"
      });
  }

  setDefaultAddress(
    data: SetDefaultAddressInput
  ): Observable<MutationResult<ResponseSetDefaultAddressDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseSetDefaultAddressDto>({
        mutation: setDefaultAddress,
        variables: data
      });
  }

  upsertAddress(
    data: AddAddressDto
  ): Observable<MutationResult<ResponseSetDefaultAddressDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseSetDefaultAddressDto>({
        mutation: upsertAddress,
        variables: data
      });
  }

  deleteAddress(
    data: DeleteAddressInput
  ): Observable<MutationResult<ResponseDeleteAddressDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseDeleteAddressDto>({
        mutation: deleteAddress,
        variables: data
      });
  }

  detailAddress(data: RequestDetailAddressDto): Observable<ApolloQueryResult<ResponseDetailAddressDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseDetailAddressDto>({
        query: detailAddress,
        variables: data,
        fetchPolicy: "no-cache"
      });
  }

}
