import {gql} from "apollo-angular";

export const listCoverageArea = gql`
  query listCoverageArea($listCoverageAreaInput: ListCoverageAreaInput!){
    listCoverageArea(listCoverageAreaInput: $listCoverageAreaInput){
      page
      limit
      total
      totalPage
      coverageAreas{
        id
        village {
          id
          name
          district {
            id
            name
            regency {
              id
              name
              province {
                id
                name
              }
            }
          }
        }
      }
    }
  }
`;
