import { Injectable } from '@angular/core';
import { Apollo, MutationResult } from 'apollo-angular';
import {
  CreatePaymentInput,
  ResponseCreatePaymentDto,
  ResponseListAvailablePayment,
  ResponseRetryPaymentDto,
  RetryPaymentInput
} from './payment.dto';
import { Observable } from 'rxjs';
import { ApolloQueryResult } from '@apollo/client';
import { ListApiTypes } from '../../../types/list-api.types';
import { createPayment, listAvailablePayment, retryPayment } from './payment.constant';

@Injectable({
  providedIn: 'root'
})
export class PaymentService {
  constructor(private apollo: Apollo) {}

  listAvailablePayment(): Observable<ApolloQueryResult<ResponseListAvailablePayment>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListAvailablePayment>({
        query: listAvailablePayment,
        fetchPolicy: "no-cache"
      });
  }

  createPayment(
    data: CreatePaymentInput
  ): Observable<MutationResult<ResponseCreatePaymentDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseCreatePaymentDto>({
        mutation: createPayment,
        variables: data
      });
  }

  retryPayment(
    data: RetryPaymentInput
  ): Observable<MutationResult<ResponseRetryPaymentDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseRetryPaymentDto>({
        mutation: retryPayment,
        variables: data
      });
  }
}
