import {Inject, Injectable, PLATFORM_ID} from '@angular/core';
import {isPlatformBrowser} from '@angular/common';

export interface ListReferralDto {
  variantSlug: string;
  referral: string;
  date: string;
  type: string;
}

@Injectable({
  providedIn: 'root'
})
export class ReferralService {
  isReady = false;

  constructor(@Inject(PLATFORM_ID) private platformId: object) {
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
    }
  }

  setReferral(variantSlug: string, referral: string, type: string) {
    if (this.isReady) {
      const localReferral = localStorage.getItem('referral');
      const listReferral: ListReferralDto[] = JSON.parse(
        localReferral ? localReferral : '[]'
      );
      const insertReferral: ListReferralDto[] = [];
      if (listReferral.length) {
        const index = listReferral.findIndex(
          (val) => val.variantSlug === variantSlug
        );
        if (index === -1) {
          listReferral.push({
            variantSlug: variantSlug,
            referral: referral,
            date: new Date().toISOString(),
            type: type
          });
          localStorage.setItem('referral', JSON.stringify(listReferral));
        } else {
          listReferral[index].referral = referral;
          listReferral[index].date = new Date().toISOString();
          listReferral[index].type = type
          localStorage.setItem('referral', JSON.stringify(listReferral));
        }
      } else {
        insertReferral.push({
          variantSlug: variantSlug,
          referral: referral,
          date: new Date().toISOString(),
          type: type
        });
        localStorage.setItem('referral', JSON.stringify(insertReferral));
      }
    }
  }

  useReferral(variantSlug: string) {
    if (this.isReady) {
      const localReferral = localStorage.getItem('referral');
      const listReferral: ListReferralDto[] = JSON.parse(
        localReferral ? localReferral : '[]'
      );
      const indexReferral = listReferral.findIndex(
        (val) => val.variantSlug === variantSlug
      );
      if (indexReferral > -1) {
        listReferral.splice(indexReferral, 1);
        localStorage.setItem('referral', JSON.stringify(listReferral));
      }
    }
  }

  findReferral(variantSlug: string): boolean {
    if (this.isReady) {
      const localReferral = localStorage.getItem('referral');
      const listReferral: ListReferralDto[] = JSON.parse(
        localReferral ? localReferral : '[]'
      );
      const haveReferral = listReferral.filter(
        (val) => val.variantSlug === variantSlug
      );
      if (haveReferral.length) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  getReferral(variantSlug: string): ListReferralDto | undefined {
    if (this.isReady) {
      const localReferral = localStorage.getItem('referral');
      const listReferral: ListReferralDto[] = JSON.parse(
        localReferral ? localReferral : '[]'
      );
      const haveReferral = listReferral.find(
        (val) => val.variantSlug === variantSlug
      );
      return haveReferral ? haveReferral : undefined;
    } else {
      return undefined;
    }
  }

  getAllReferral(): ListReferralDto[] {
    if (this.isReady) {
      const localReferral = localStorage.getItem('referral');
      return JSON.parse(localReferral ? localReferral : '[]');
    } else {
      return [];
    }
  }

  getLastReferral(): ListReferralDto {
    if (this.isReady) {
      const localReferral = localStorage.getItem('referral');
      const listReferral: ListReferralDto[] = JSON.parse(
        localReferral ? localReferral : '[]'
      );
      return listReferral[listReferral.length - 1];
    } else {
      return {} as ListReferralDto;
    }
  }

  validateReferral() {
    if (this.isReady) {
      const localReferral = localStorage.getItem('referral');
      const dataReferral: ListReferralDto[] = JSON.parse(
        localReferral ? localReferral : '[]'
      );
      dataReferral.forEach((val, index) => {
        const date = new Date(val.date);
        const today = new Date();
        if (
          date.getDate() < today.getDate() - 7 ||
          date.getMonth() < today.getMonth()
        ) {
          dataReferral.splice(index, 1);
        }
      });
      localStorage.setItem('referral', JSON.stringify(dataReferral));
    }
  }

  getLastReferralByType(type: string) {
    if (this.isReady) {
      const localReferral = localStorage.getItem('referral');
      const listReferral: ListReferralDto[] = JSON.parse(
        localReferral ? localReferral : '[]'
      );
      const filterType = listReferral.filter((val) => val.type === type)
      return filterType[filterType.length - 1];
    } else {
      return {} as ListReferralDto;
    }
  }
}
