import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";

@Component({
  selector: "lib-quantity-inputs",
  templateUrl: "./quantity-input.component.html",
  styleUrls: ["./quantity-input.component.scss"]
})
export class QuantityInputComponent implements OnInit {
  @Input() isButtonDisable = false;
  @Input() valueQuantity = 1;
  @Input() quantity = 0;

  @Input() set qty(data: number) {
    this.quantity = data
    this.checkQuantity()
  }

  @Input() minQuantity = 1;
  @Input() width = "auto";
  @Input() height = "100%";
  @Output() qtyChange = new EventEmitter<number>();
  disableMin = false;
  disablePlus = false;

  ngOnInit() {
    if (this.minQuantity === 0) {
      this.minQuantity = 1
    }

    if (!this.quantity) {
      this.disableMin = true;
      this.disablePlus = true;
    } else {
      this.checkQuantity()
    }
  }

  reduceQuantity() {
    this.valueQuantity = this.valueQuantity - 1;
    this.checkQuantity();
    this.qtyChange.emit(this.valueQuantity);
  }

  addQuantity() {
    this.valueQuantity = this.valueQuantity + 1;
    this.checkQuantity();
    this.qtyChange.emit(this.valueQuantity);
  }

  checkQuantity() {
    this.disableMin = false;
    this.disablePlus = false;
    if (this.valueQuantity <= (this.minQuantity ?? 1)) {
      this.disableMin = true;
    }
    if (this.valueQuantity >= this.quantity) {
      this.disablePlus = true;
    }
  }
}
