import { Injectable } from '@angular/core';
import { ListApiTypes } from '../../../types/list-api.types';
import { Apollo, MutationResult } from 'apollo-angular';
import { Observable } from 'rxjs';

import {
  AddNoteInput,
  CheckUncheckCartInput,
  CheckUncheckCartItemInput,
  CreateCartInputDto,
  CreateCartWithMultipleProductInput,
  DeleteCartItemInput,
  EditCartItemInput,
  RefreshPriceInput,
  ResponseAddNote,
  ResponseCheckCartDto,
  ResponseCheckUncheckCart,
  ResponseCheckUncheckCartItemDto,
  ResponseCreateCartDto,
  ResponseDeleteCartItem,
  ResponseEditCartItem,
  ResponseListCartDto,
  ResponseTotalCart
} from './cart.dto';
import {
  createCart,
  listCart,
  checkCart,
  checkUncheckCartItem,
  checkUncheckCart,
  editCartItem,
  deleteCartItem,
  addNote,
  totalCart,
  refreshPrice,
  createCartMultipleProduct
} from './cart.constant';
import { ApolloQueryResult } from '@apollo/client';

@Injectable({
  providedIn: 'root'
})
export class CartService {
  constructor(private apollo: Apollo) {}

  createCart(
    data: CreateCartInputDto
  ): Observable<MutationResult<ResponseCreateCartDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseCreateCartDto>({
        mutation: createCart,
        variables: data
      });
  }
  createCartWithMultipleProduct(
    data: CreateCartWithMultipleProductInput
  ): Observable<MutationResult<ResponseCreateCartDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseCreateCartDto>({
        mutation: createCartMultipleProduct,
        variables: data
      });
  }

  listCartQuery(): Observable<ApolloQueryResult<ResponseListCartDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseListCartDto>({
        query: listCart,
        fetchPolicy: 'no-cache'
      });
  }

  checkCartQuery(): Observable<ApolloQueryResult<ResponseCheckCartDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseCheckCartDto>({
        query: checkCart,
        fetchPolicy: 'no-cache'
      });
  }

  checkUncheckCartItem(
    data: CheckUncheckCartItemInput
  ): Observable<MutationResult<ResponseCheckUncheckCartItemDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseCheckUncheckCartItemDto>({
        mutation: checkUncheckCartItem,
        variables: data
      });
  }

  checkUncheckCart(
    data: CheckUncheckCartInput
  ): Observable<MutationResult<ResponseCheckUncheckCart>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseCheckUncheckCart>({
        mutation: checkUncheckCart,
        variables: data
      });
  }

  editCartItem(
    data: EditCartItemInput
  ): Observable<MutationResult<ResponseEditCartItem>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseEditCartItem>({
        mutation: editCartItem,
        variables: data
      });
  }

  deleteCartItem(
    data: DeleteCartItemInput
  ): Observable<MutationResult<ResponseDeleteCartItem>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseDeleteCartItem>({
        mutation: deleteCartItem,
        variables: data
      });
  }

  addNote(data: AddNoteInput): Observable<MutationResult<ResponseAddNote>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseAddNote>({
        mutation: addNote,
        variables: data
      });
  }

  totalCart(): Observable<ApolloQueryResult<ResponseTotalCart>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .query<ResponseTotalCart>({
        query: totalCart,
        fetchPolicy: 'no-cache'
      });
  }

  refreshPrice(
    data: RefreshPriceInput
  ): Observable<MutationResult<ResponseCreateCartDto>> {
    return this.apollo
      .use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL)
      .mutate<ResponseCreateCartDto>({
        mutation: refreshPrice,
        variables: data
      });
  }
}
