/* eslint-disable @typescript-eslint/no-explicit-any */
import {
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest
} from '@angular/common/http';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { AuthData } from '../shared/data/auth.data';
import * as dayjs from 'dayjs';
import { TokenService } from '@youtap-web-lib';
import { Router } from '@angular/router';
import { WindowActionService } from '../shared/service/window-action.service';

@Injectable()
export class AuthInterceptors implements HttpInterceptor {
  constructor(
    private authData: AuthData,
    private router: Router,
    private tokenService: TokenService,
    private windowActionService: WindowActionService
  ) {}

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    /** check expired */
    if (this.authData.expiredDate) {
      this.checkExpired().then(async (val) => {
        if (val && !this.authData.isRefresh) {
          this.authData.isRefresh = true;
          await this.refreshToken();
        }
      });
    }

    /** set auth header */
    const village = Object.keys(this.authData.location).length
      ? this.authData.location.village
      : this.authData.defaultVillage;
    const token = this.authData.withdrawToken
      ? this.authData.withdrawToken
      : this.authData.token;
    const detail = this.authData.location;
    if (req.method !== 'JSONP' && !req.url.includes('maps.googleapis.com')) {
      req = req.clone({
        setHeaders: {
          // 'Content-Type': 'application/json',
          'x-platform-id': 'WEB',
          'x-timezone': 'Asia/Jakarta',
          ...(token && { Authorization: `${token}` }),
          ...((!token || !detail.id) && { 'x-village-id': village.id })
        }
      });
    }
    return next.handle(req).pipe();
    // catchError((err: HttpErrorResponse) => {
    //   if (err && err.status === 401) {
    //   } else if (err && err.status === 400) {
    //     return throwError(err.error);
    //   } else {
    //     return throwError(err.error);
    //   }
    // })
  }

  checkExpired(): Promise<boolean> {
    const dateNow = dayjs(new Date());
    const expiredDate = dayjs(this.authData.expiredDate);
    const range = expiredDate.diff(dateNow, 'days');
    return new Promise((resolve) => {
      if (range <= 15 && range > 0) {
        resolve(true);
      } else if (range <= 0) {
        localStorage.clear();
        this.windowActionService.reload();
        resolve(false);
      } else {
        resolve(false);
      }
    });
  }

  refreshToken() {
    this.tokenService
      .refreshToken()
      .toPromise()
      .then(({ data }: any) => {
        this.authData.token = data.refreshToken.authToken.accessToken;
        this.authData.expiredDate = data.refreshToken.authToken.expiredAt;
        this.windowActionService.reload();
      })
      .catch(() => {
        localStorage.clear();
        this.windowActionService.reload();
      });
  }
}
