import { Injectable } from '@angular/core';
import {Apollo, } from "apollo-angular";
import { Observable } from 'rxjs';
import {
  ListCategoryDto,
  RequestDetailCategoryDto,
  RequestDetailCategoryV2Dto,
  ResponseDetailCategoryDto,
  ResponseDetailCategoryV2Dto,
  ResponseListCategoryBranchDto,
  ResponseListCategoryDto,
  ResponseListCategoryParentDto
} from "./category.dto";
import { ApolloQueryResult } from '@apollo/client';
import { ListApiTypes } from '../../../types/list-api.types';
import {
  listCategoryParent,
  listCategory,
  listCategoryBranch,
  detailCategory,
  detailCategoryV2
} from "./category.constant";

@Injectable({
  providedIn: 'root'
})
export class CategoryService {

  constructor(private apollo: Apollo) { }


  listCategoryParent(data: ListCategoryDto): Observable<ApolloQueryResult<ResponseListCategoryParentDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseListCategoryParentDto>({
      fetchPolicy: 'no-cache',
      query: listCategoryParent,
      variables: data
    })
  }

  listCategory(data: ListCategoryDto): Observable<ApolloQueryResult<ResponseListCategoryDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseListCategoryDto>({
      query: listCategory,
      variables: data
    })
  }

  listCategoryBranch(data: ListCategoryDto): Observable<ApolloQueryResult<ResponseListCategoryBranchDto>> {
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseListCategoryBranchDto>({
      fetchPolicy: 'no-cache',
      query: listCategoryBranch,
      variables: data
    })
  }

  detailCategory(data: RequestDetailCategoryDto): Observable<ApolloQueryResult<ResponseDetailCategoryDto>>{
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseDetailCategoryDto>({
      query: detailCategory,
      variables: data
    })
  }

  detailCategoryV2(data: RequestDetailCategoryV2Dto): Observable<ApolloQueryResult<ResponseDetailCategoryV2Dto>>{
    return this.apollo.use(ListApiTypes.SUPPLY_CHAIN_PUBLIC_API_GRAPHQL_URL).query<ResponseDetailCategoryV2Dto>({
      query: detailCategoryV2,
      variables: data
    })
  }

}
